<template>
  <div class="fortuneTreeJoin">
    <div class="header">
      <div class="title">
        <i onClick="window.history.back()"></i>
        <p>财富树信息</p>
      </div>
      <p>xx财富树</p>
    </div>
    <div class="list">
      <ul>
        <li>
          <i></i>
          <span>消费使用币种</span>
          <p>USDT</p>
        </li>
        <li>
          <i></i>
          <span>层级</span>
          <p>8</p>
        </li>
        <li>
          <i></i>
          <span>金额</span>
          <p>升一级x币、升二级x个币、升三级x个币</p>
        </li>
        <li>
          <i></i>
          <span>推广制度</span>
          <p>一推x，太阳线推广（无推广限制）</p>
        </li>
        <li>
          <i></i>
          <span>当前成长级</span>
          <p>xx级</p>
        </li>
        <li>
          <i></i>
          <span>当前参与人数</span>
          <p>xx人数</p>
        </li>
      </ul>
    </div>
    <button>加入</button>
  </div>
</template>

<script>
export default {
  name: "FortuneTreeJoin"
};
</script>

<style lang='less' scoped>
.fortuneTreeJoin {
  // height: 110vh;
  padding-bottom: 0.3rem;
  .header {
    width: 3.75rem;
    height: 2.2rem;
    background: url("../../../static/xinxi_background@2x.png");
    background-size: cover;
    .title {
      overflow: hidden;
      position: relative;
      p {
        font-weight: 500;
        color: rgba(51, 51, 51, 1);
        margin-top: 0.12rem;
      }
      i {
        background: url("../../../static/leftarrow_black_icon@2x.png");
        background-size: cover;
        width: 0.16rem;
        height: 0.16rem;
        position: absolute;
        left: 0.17rem;
        top: 0.13rem;
      }
    }
    p {
      font-size: 0.15rem;
      color: rgba(51, 51, 51, 1);
      margin-top: 1.29rem;
    }
  }
  .list {
    ul {
      overflow: hidden;
      li {
        position: relative;
        height: 0.5rem;
        margin-top: 0.2rem;
        i {
          display: inline-block;
          width: 0.27rem;
          height: 0.27rem;
          background: url("../../../static/bizhong_icon@2x.png");
          background-size: cover;
          position: absolute;
          left: 0.87rem;
          top: 0;
        }
        span {
          font-size: 0.15rem;
          font-weight: bold;
          color: rgba(247, 181, 0, 1);
          position: absolute;
          left: 1.29rem;
          top: 0.04rem;
        }
        p {
          font-size: 0.1rem;
          color: rgba(119, 118, 118, 1);
          width: 1.14rem;
          position: absolute;
          left: 1.29rem;
          top: 0.28rem;
          text-align: left;
        }
      }
      li:nth-child(2) {
        i {
          background: url("../../../static/cengji_icon@2x.png");
          background-size: cover;
        }
      }
      li:nth-child(3) {
        i {
          background: url("../../../static/jiner_icon@2x.png");
          background-size: cover;
        }
      }
      li:nth-child(4) {
        i {
          background: url("../../../static/zhidu_icon@2x.png");
          background-size: cover;
        }
      }
      li:nth-child(5) {
        i {
          background: url("../../../static/dengji_icon@2x.png");
          background-size: cover;
        }
      }
      li:nth-child(6) {
        i {
          background: url("../../../static/renshu_icon@2x.png");
          background-size: cover;
        }
      }
    }
  }
  button {
    width: 3.45rem;
    height: 0.45rem;
    background: rgba(227, 60, 61, 1);
    border-radius: 0.03rem;
    font-size: 0.17rem;
    color: rgba(245, 245, 245, 1);
    margin-top: 0.2rem;
  }
}
</style>



