<template>
  <div class="inviteFriendGrabRedpack">
    <Header title class="component-header"></Header>
    <div class="header">
      <img src="../../../static/images/yaoqing_bg02@2x.png" alt />
    </div>
    <div class="activeRule">
      <img src="../../../static/huodong_image@2x.png" alt />
      <p>邀请您的朋友加入，您的朋友无论是抢红包还是当群主，您拿一代盈利的6%，拿二代盈利的5%，拿三代盈利的4%。支持人民币充值提现！使用区块链智能合约做到公平公正、任何人无法作弊！</p>
    </div>
    <div class="main">
      <div class="main-content">
        <div class="left">
          <p class="title">我的邀请码</p>
          <p class="code" id="inviteCode">WHK123456</p>
          <button @click="copyInvite">复制</button>
        </div>
        <div class="right">
          <p class="title">红包房间号</p>
          <p class="code" id="roomCode">3695456875</p>
          <button @click="copyRoom">复制</button>
        </div>
      </div>
      <hr />
      <img src="../../../static/erweima_image@2x.png" alt />
      <p class="bottom">扫描二维码，用邀请码注册后搜索房间号，马上抢红包!</p>
    </div>
  </div>
</template>

<script>
import Header from "../customize/Header";
import { copyFun } from '../../assets/js/unit';
export default {
  name: "InviteFriendGrabRedpack",
  data() {
    return {};
  },
  components: {
    Header
  },
  methods: {
    copyInvite(){
      copyFun('inviteCode')
    },
    copyRoom(){
      copyFun('roomCode')
    },
  }
};
</script>

<style lang='less' scoped>
.inviteFriendGrabRedpack {
  background-color: #ffeee6;
  .component-header {
    background-color: transparent;
  }
  .header {
    img {
      width: 3.75rem;
    }
  }
  .activeRule {
    width: 3.12rem;
    height: 1.5rem;
    background-color: #fff;
    margin: auto;
    transform: translateY(-0.36rem);
    overflow: hidden;
    img {
      width: 1.16rem;
      margin-top: 0.26rem;
    }
    p {
      font-size: 0.12rem;
      font-family: SimHei;
      color: rgba(108, 71, 47, 1);
      line-height: 0.15rem;
      text-align: left;
      padding: 0.16rem 0.15rem 0 0.15rem;
    }
  }
  .main {
    width: 3.12rem;
    background: rgba(255, 255, 255, 1);
    margin: auto;
    transform: translateY(-0.2rem);
    overflow: hidden;
    .main-content {
      display: flex;
      .left, .right{
        flex:1;
      }
      .title {
        font-size: 0.15rem;
        font-weight: bold;
        margin-top: 0.25rem;
      }
      .code {
        font-size: 0.14rem;
        font-weight: bold;
        color: rgba(227, 60, 61, 1);
        margin-top: 0.15rem;
      }
      button {
        width: 0.63rem;
        line-height: 0.2rem;
        background: rgba(227, 60, 61, 1);
        border-radius: 0.02rem;
        margin-top: 0.1rem;
        font-size: 0.13rem;
        color: rgba(255, 255, 255, 1);
      }
    }
    hr {
      width: 2.3rem;
      border: 0.5px dashed rgba(160, 160, 160, 1);
      margin: 0.2rem auto;
    }
    img {
      width: 0.74rem;
    }
    .bottom {
      font-size: 8px;
      margin-top: 0.07rem;
      margin-bottom: 0.2rem;
      color: rgba(10, 0, 0, 1);
      opacity: 0.7;
    }
  }
}
</style>



