<template>
  <div class="signin">
    <img src="../assets/app_cancel_icon@2x.png" alt class="closs" @click="back" />
    <img src="../assets/home_logo@2x.png" alt class="logo" />
    <p class="title">EOSATM</p>
    <ul class="list">
      <li>
        <img src="../assets/login_user_icon@2x.png" alt />
        <input type="text" placeholder="请输入用户名" />
      </li>
      <li>
        <img src="../assets/login_password_icon@2x.png" alt />
        <input type="password" placeholder="请输入密码" />
      </li>
    </ul>
    <div class="login_box"><button @click="tapLogin">登录</button></div>
    <div class="fast_reg"><router-link to="/signon">快速注册</router-link></div>
  </div>
</template>

<script>
export default {
  name: "Signin",
  data() {
    return {
      msg: "1111111111111111111"
    };
  },
  methods: {
    back: function(event) {
      window.history.back();
    },
    tapLogin(){
      this.$router.push('mi')
      this.$store.commit('checkLogin')
    },
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='less' scoped>
.signin {
  height: 100vh;
  background-color: #f5f5f5;
  position: relative;
  .closs {
    width: 0.16rem;
    position: absolute;
    top: 0.15rem;
    right: 0.15rem;
  }
  .logo {
    width: 0.87rem;
    padding-top: .5rem;
    display: block;
    margin: auto;
  }
  .title {
    font-size: 0.31rem;
    font-family: Noto Sans S Chinese;
    font-weight: bold;
    color: rgba(0, 9, 18, 1);
    margin: auto;
    padding-top: .16rem;
  }
  .list {
    padding: .5rem .15rem .05rem;
    li {
      width: 100%;
      height: 0.45rem;
      background: rgba(255, 255, 255, 1);
      border-radius: 0.03rem;
      position: relative;
      margin-bottom: .15rem;
      img {
        width: 0.16rem;
        position: absolute;
        left: .14rem;
        top: .14rem;
      }
      input {
        height: .43rem;
        font-size: 0.15rem;
        color: rgba(181, 184, 199, 1);
        border: none;
        position: absolute;
        left: .45rem;
        top: 0;
        width: 80%;
      }
    }
  }
  .login_box{
    padding: 0 .15rem;
    button {
      width: 100%;
      height: .45rem;
      background: rgba(227, 60, 61, 1);
      border-radius: 0.03rem;
      letter-spacing: .01rem;
      color: #f5f5f5;
      font-size: .17rem;
    }
  }
  .fast_reg{
    padding: .15rem;
    text-align: right;
    a {
      font-size: 0.13rem;
      color: rgba(227, 60, 61, 1);
    }
  }
}
</style>



