<template>
  <div class="ruleNotice">
    <Header title="规则与公告"></Header>
    <div class="main">
      <div class="left">
        <p>规则</p>
      </div>
      <div class="right">
        <p>公告</p>
      </div>
    </div>
  </div>
</template>

<script>
import Header from "../customize/Header";
export default {
  name: "RuleNotice",
  data() {
    return {};
  },
  components: {
    Header
  },
  methods: {}
};
</script>

<style lang="less" scoped>
.ruleNotice {
  overflow: hidden;
  .main {
    margin-top: 0.44rem;
    display: flex;
    justify-content: space-between;
    padding: 0.1rem 0.15rem;
    div {
      width: 1.65rem;
      height: 0.84rem;
      border-radius: 0.1rem;
      padding: 0.23rem 0 0 0.16rem;
      box-sizing: border-box;
    }
    p {
      font-size: 0.15rem;
      font-weight: 500;
      color: rgba(255, 255, 255, 1);
      text-align: left;
    }
    .left {
      background: url("../../../static/guize_image@2x.png");
      background-size: cover;
    }
    .right {
      background: url("../../../static/gonggao_image@2x.png");
      background-size: cover;
    }
  }
}
</style>


