<template>
  <div class="tabbar">
    <div class="create">
      <div class="close">
        <img src="../assets/found_btn_min@2x.png" alt @click="xiaShow" />
      </div>
      <div class="create-hong">
        <router-link to="redpackGroup">
          <img src="../assets/found_image_hongbao@2x.png" alt />
          <p>创建红包群</p>
        </router-link>
      </div>
      <div class="hr"></div>
      <div class="create-hong">
        <router-link to="fortuneTree">
          <img src="../assets/home_logo@2x.png" alt />
          <p>创建财富数</p>
        </router-link>
      </div>
    </div>
    <div class="contain">
      <router-view></router-view>
    </div>
    <div class="bar">
      <ul>
        <li @click="handSu" class="active" id="normalOne">
          <router-link to="index">
            <img v-bind:src="suye" alt />
            <p>首页</p>
          </router-link>
        </li>
        <li @click="handHb" id="normalTwo">
          <router-link to="hongbaoqing">
            <img v-bind:src="hong" alt />
            <p>红包群</p>
          </router-link>
        </li>
        <li @click="show">
          <img src="../assets/tabar_found_btn@2x.png" alt />
        </li>
        <li @click="handCai" id="normalThree">
          <router-link to="caifusu">
            <img v-bind:src="cai" alt />
            <p>财富树</p>
          </router-link>
        </li>
        <li @click="handMi" id="normalFour">
          <router-link to="mi">
            <img v-bind:src="mi" alt />
            <p>我的</p>
          </router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "Tabbar",
  data() {
    return {
      help: false,
      suye: "../../static/tabar_home_selected@2x.png",
      hong: "../../static/tabar_redpacke_normal@2x.png",
      cai: "../../static/tabar_tree_normal@2x.png",
      mi: "../../static/tabar_me_normal@2x.png"
    };
  },
  methods: {
    show: function(e) {
      let elcre = document.querySelector(".create");
      elcre.style.height = "1.59rem";
    },
    xiaShow: function(event) {
      let elcre = document.querySelector(".create");
      elcre.style.height = "0rem";
    },
    handSu: function(event) {
      this.suye = "../../static/tabar_home_selected@2x.png";
      this.hong = "../../static/tabar_redpacke_normal@2x.png";
      this.cai = "../../static/tabar_tree_normal@2x.png";
      this.mi = "../../static/tabar_me_normal@2x.png";
      $('#normalOne').addClass('active').siblings().removeClass('active');
      this.xiaShow()
    },
    handHb: function(event) {
      this.suye = "../../static/tabar_home_normal@2x.png";
      this.hong = "../../static/tabar_redpacket_selected@2x.png";
      this.cai = "../../static/tabar_tree_normal@2x.png";
      this.mi = "../../static/tabar_me_normal@2x.png";
      $('#normalTwo').addClass('active').siblings().removeClass('active');
      this.xiaShow()
    },
    handCai: function(event) {
      this.suye = "../../static/tabar_home_normal@2x.png";
      this.hong = "../../static/tabar_redpacke_normal@2x.png";
      this.cai = "../../static/tabar_tree_selected@2x.png";
      this.mi = "../../static/tabar_me_normal@2x.png";
      $('#normalThree').addClass('active').siblings().removeClass('active');
      this.xiaShow()
    },
    handMi: function(event) {
      this.suye = "../../static/tabar_home_normal@2x.png";
      this.hong = "../../static/tabar_redpacke_normal@2x.png";
      this.cai = "../../static/tabar_tree_normal@2x.png";
      this.mi = "../../static/tabar_me_selected@2x.png";
      $('#normalFour').addClass('active').siblings().removeClass('active');
      this.xiaShow()
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='less' scoped>
.tabbar {
  position: relative;
  // height: 100vh;
  .create {
    width: 3.75rem;
    height: 0rem;
    overflow: hidden;
    transition: all 0.5s;
    border-radius: 0.2rem 0.2rem 0rem 0rem;
    box-shadow: 0px -2px 6px 0px rgba(0, 0, 0, 0.1);
    position: fixed;
    left: 0;
    bottom: 0.48rem;
    background-color: #fff;
    z-index: 999;
    .close {
      img {
        width: 0.27rem;
      }
    }
    .create-hong {
      width: 1.4rem;
      margin-left: 1.17rem;
      height: 0.45rem;
      background-color: #fff;
      position: relative;
      img {
        width: 0.45rem;
        position: absolute;
        left: 0;
      }
      p {
        position: absolute;
        right: 0.06rem;
        height: 0.14rem;
        font-size: 0.15rem;
        color: rgba(19, 19, 19, 1);
        line-height: 0.45rem;
      }
    }
    .hr {
      width: 3.75rem;
      height: 0.01rem;
      background: rgba(238, 238, 238, 1);
      margin: 0.1rem 0 0.1rem 0;
    }
  }
  .contain {
    // height: 100vh;
    padding-bottom: 0.48rem;
    box-sizing: border-box;
  }
  .bar {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 3.75rem;
    height: 0.48rem;
    background: rgb(255, 255, 255);
    ul {
      display: flex;
      box-shadow:0 1px 2px 0 rgba(0, 0, 0, 0.24);
      li {
        width: 0.75rem;
        height: 0.48rem;
        padding-top: 0.06rem;
        a{
          display: block;
        }
        a:active {
          -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
        }
        img {
          width: 0.2rem;
          display: block;
          margin: auto;
        }
        p {
          font-size: 0.1rem;
          color:rgba(151,151,151,1);
          margin-top: .02rem;
        }
      }
      li:nth-child(3) {
        img {
          width: 0.36rem;
        }
      }
      .active{
        p{
          color:rgba(34,34,34,1);
        }
      }
    }
  }
}
</style>



