<template>
  <div class="content">
    <div class="mainTitle">一级用户共xx人</div>
    <div class="tabContain">
      <ul class="tab">
        <li class="active" id="sub-tabOne" @click="tapTab">一级用户</li>
        <li @click="tapTab" id="sub-tabTwo">二级用户</li>
        <li id="sub-tabThree" @click="tapTab">三级用户</li>
      </ul>
    </div>
    <component :is="currentTap"></component>
  </div>
</template>

<script>
import OneLevel from "./OneLevel";
import TwoLevel from "./TwoLevel";
import ThreeLevel from "./ThreeLevel";
export default {
  name: "Subordinate",
  data() {
    return {
      currentTap: "OneLevel"
    };
  },
  components: {
    OneLevel,
    TwoLevel,
    ThreeLevel,
  },
  methods: {
    tapTab(e) {
      const el = e.target.id;
      if (el === "sub-tabOne") {
        $("#sub-tabOne")
          .addClass("active")
          .siblings()
          .removeClass("active");
        this.currentTap = "OneLevel";
      } else if (el === "sub-tabTwo") {
        $("#sub-tabTwo")
          .addClass("active")
          .siblings()
          .removeClass("active");
        this.currentTap = "TwoLevel";
      } else if (el === "sub-tabThree") {
        $("#sub-tabThree")
          .addClass("active")
          .siblings()
          .removeClass("active");
        this.currentTap = "ThreeLevel";
      }
    }
  }
};
</script>

<style lang="less" scoped>
@baseHr: #e7e7e7;
.content {
  .mainTitle {
    font-size: 0.14rem;
    color: rgba(5, 5, 5, 1);
    line-height: 0.3rem;
  }
  .tabContain {
    .tab {
      display: flex;
      li {
        line-height: 0.3rem;
        flex: 1;
        position: relative;
        font-size: 0.13rem;
        color:rgba(81,82,84,1);
        &::after {
          content: "";
          display: inline-block;
          position: absolute;
          left: 0.51rem;
          top: 0.28rem;
          width: 0.0rem;
          height: 0.01rem;
          background:rgba(227,60,61,1);
          border-radius: 0.02rem;
        }
      }
      .active {
        &::after {
          width: 0.25rem;
        }
      }
    }
  }
}
</style>


