<template>
  <div class="content">
    <div class="notNote" v-show="!isNote">
      <img src="../../../static/wujilv_image@2x.png" alt />
      <p>无记录，赶快去收藏~</p>
    </div>
    <ul v-show="isNote">
      <li>
        <div class="icon">
          <img src="../../../static/icon_cz_orange@2x.png" alt />
        </div>
        <div class="left">
          <p class="top">ETH</p>
          <p class="bottom">
            2019.05.07 19:12:50
            <span>充值成功</span>
          </p>
        </div>
        <div class="right">
          <p>数量: 0.000012</p>
        </div>
        <hr />
      </li>
      <li>
        <div class="icon">
          <img src="../../../static/icon_cz_orange@2x.png" alt />
        </div>
        <div class="left">
          <p class="top">ETH</p>
          <p class="bottom">
            2019.05.07 19:12:50
            <span class="fail">充值失败</span>
          </p>
        </div>
        <div class="right">
          <p>数量: 0.000012</p>
        </div>
        <hr />
      </li>
      <li>
        <div class="icon">
          <img src="../../../static/icon_cz_orange@2x.png" alt />
        </div>
        <div class="left">
          <p class="top">ETH</p>
          <p class="bottom">
            2019.05.07 19:12:50
            <span>充值成功</span>
          </p>
        </div>
        <div class="right">
          <p>数量: 0.000012</p>
        </div>
        <hr />
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data(){
    return {
      isNote: false
    }
  }
};
</script>

<style lang="less" scoped>
@baseHr: #e7e7e7;
.content {
  .notNote {
    overflow: hidden;
    img {
      width: 2rem;
      margin-top: 0.7rem;
    }
    p {
      font-size: 0.14rem;
      color: rgba(102, 102, 102, 1);
      line-height: 0.5rem;
    }
  }
  ul {
    li {
      display: flex;
      height: 0.57rem;
      text-align: left;
      position: relative;
      .icon {
        flex: 1;
        align-items: center;
        display: flex;
        img {
          width: 0.15rem;
          margin-left: 0.15rem;
        }
      }
      .left {
        justify-content: center;
        display: flex;
        flex-direction: column;
        flex: 6;
        .top {
          font-size: 0.14rem;
          color: rgba(19, 19, 19, 1);
        }
        .bottom {
          font-size: 0.12rem;
          color: rgba(173, 173, 173, 1);
          span {
            font-size: 0.11rem;
            color: rgba(93, 196, 58, 1);
          }
          .fail {
            color: rgba(245, 83, 83, 1);
          }
        }
      }
      .right {
        flex: 3;
        p {
          font-size: 0.14rem;
          color: rgba(146, 146, 146, 1);
          margin-top: 0.13rem;
        }
      }
      hr {
        border: 0.5px solid @baseHr;
        width: 3.45rem;
        position: absolute;
        margin: 0;
        left: 0.15rem;
        top: 0.57rem;
      }
    }
  }
}
</style>


