<template>
  <div class="signonSuccessNext">
    <!-- <img src="../../../static/leftarrow_black_icon@2x.png" alt class="closs" @click="back" /> -->
    <img src="../../assets/home_logo@2x.png" alt class="logo" />
    <p class="title">EOSATM</p>
    <ul class="list">
      <li>
        <img src="../../assets/login_user_icon@2x.png" alt />
        <input type="text" placeholder="请输入用户名" v-model="username" />
      </li>
      <li>
        <img src="../../assets/login_password_icon@2x.png" />
        <input type="password" placeholder="请输入密码" v-model="password" />
      </li>
    </ul>
    <button @click="tapLogin">登录</button>
    <p
      class="backLast"
      @click="() => this.$router.push({ name: 'SignonSuccess', params: { title: '注册成功', isShowBtn: true }})"
    >返回上一步备份账号和密码</p>
  </div>
</template>

<script>
export default {
  name: "SignonSuccessNext",
  data() {
    return {
      username: "",
      password: ""
    };
  },
  methods: {
    // back: function(event) {
    //   window.history.back();
    // },
    tapLogin() {
      this.$axios
        .post("user/login", {
          username: this.username,
          password: this.password,
        })
        .then(res => {
          const data = res.data.data;
          if (res.data.status === 1) {
            this.$store.state.isLogin = true;
            this.$store.state.token = data.token;
            this.$store.state.sessionid = data.sessionid;
            this.$router.push({path:'mi', query:{username: this.username}});
          } else {
            this.$toast("请输入正确的帐户名和密码");
          }
        })
        .catch(err => {
          this.$toast("访问地址有误");
        });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='less' scoped>
.signonSuccessNext {
  height: 100vh;
  background-color: rgb(243, 243, 243);
  position: relative;
  overflow: hidden;
  // .closs {
  //   width: 0.16rem;
  //   position: absolute;
  //   top: 0.15rem;
  //   left: 0.15rem;
  // }
  .logo {
    width: 0.87rem;
    position: absolute;
    top: 0.66rem;
    right: 1.44rem;
  }
  .title {
    width: 1.27rem;
    height: 0.24rem;
    font-size: 0.31rem;
    font-family: Noto Sans S Chinese;
    font-weight: bold;
    color: rgba(0, 9, 18, 1);
    line-height: 0.15rem;
    position: absolute;
    top: 1.7rem;
    right: 1.24rem;
  }
  .list {
    li {
      width: 3.45rem;
      height: 0.45rem;
      background: rgba(255, 255, 255, 1);
      border-radius: 0.05rem;
      position: absolute;
      top: 2.65rem;
      right: 0.15rem;
      img {
        width: 0.16rem;
        position: absolute;
        left: 0.15rem;
        bottom: 0.13rem;
      }
      input {
        height: 0.14rem;
        font-size: 0.15rem;
        color: rgba(181, 184, 199, 1);
        line-height: 0.15rem;
        position: absolute;
        left: 0.45rem;
        bottom: 0.13rem;
        border: none;
      }
    }
    li:nth-child(2) {
      top: 3.25rem;
    }
  }
  button {
    width: 3.45rem;
    height: 0.45rem;
    background: rgba(227, 60, 61, 1);
    border-radius: 0.05rem;
    position: absolute;
    top: 3.95rem;
    right: 0.15rem;
  }
  .backLast {
    margin-top: 4.54rem;
    font-size: 0.13rem;
    color: rgba(34, 34, 34, 1);
    line-height: 0.15rem;
  }
}
</style>



