<template>
  <div class="fillingupRecords">
    <Header title="充提记录"></Header>
    <ul class="tab">
      <li :class="{active: isFill}" @click="tapFill">充值记录</li>
      <li :class="{active: isUp}" @click="tapUp">提币记录</li>
    </ul>
    <component v-bind:is="currentTab"></component>
  </div>
</template>

<script>
import Header from "../customize/Header";
import FillComponent from "./FillComponent";
import UpComponent from "./UpComponent";
export default {
  name: "FillingupRecords",
  data() {
    return {
      currentTab: "FillComponent",
      isFill: true,
      isUp: false,
    };
  },
  components: {
    Header,
    FillComponent,
    UpComponent
  },
  methods: {
    tapFill(){
      this.isFill = true
      this.isUp = false
      this.currentTab = 'FillComponent'
    },
    tapUp(){
      this.isFill = false
      this.isUp = true
      this.currentTab = 'UpComponent'
    },
  }
};
</script>

<style lang='less' scoped>
.fillingupRecords {
  background-color: rgb(248, 248, 248);
  height: 100vh;
  overflow: hidden;
  .tab {
    margin-top: 0.44rem;
    height: 0.49rem;
    display: flex;
    li {
      line-height: 0.49rem;
      flex: 1;
      position: relative;
      color: rgba(5, 5, 5, 1);
    }
    .active {
      color: rgba(227, 60, 61, 1);
      &::after {
        content: '';
        display: inline-block;
        position: absolute;
        left: 0.47rem;
        top: 0.47rem;
        width: 0.9rem;
        height: 0.02rem;
        background: rgba(227, 60, 61, 1);
      }
    }
  }
}
</style>



