<template>
  <div class="setNickname">
    <Header title="设置昵称"></Header>
    <form action>
      <input type="text" name="nickName" placeholder="avdcc123" />
    </form>
    <button>取消</button>
  </div>
</template>

<script>
import Header from "../customize/Header";
export default {
  name: "SetNickname",
  components: {
    Header
  },
  data() {
    return {};
  },
  methods: {}
};
</script>

<style lang="less" scoped>
.setNickname {
  margin-top: 0.44rem;
  background-color: rgb(245, 245, 245);
  form {
    overflow: hidden;
    input {
      margin-top: 0.1rem;
      width: 3.45rem;
      height: 0.46rem;
      background: rgba(255, 255, 255, 1);
      border-radius: 0.03rem;
      padding-left: 0.1rem;
      box-sizing: border-box;
    }
  }
  button {
    margin-top: 0.15rem;
    width: 3.45rem;
    background: rgba(227, 60, 61, 1);
    border-radius: 0.03rem;
    font-size: 0.17rem;
    line-height: 0.45rem;
  }
}
</style>


