<template>
  <div class="redpackGroup">
    <div class="title">
      <img src="../../assets/btn_back_white@2x.png" alt onClick='window.history.back()'/>
      <p>创建红包群</p>
    </div>
    <form action class="createRedGroupForm">
      <div class="item">
        <label for>群名称：</label>
        <input type="text" name="groupName" id placeholder="请输入群名称" />
        <div class="hr"></div>
      </div>
      <div class="item">
        <label for>红包总金额：</label>
        <input type="text" name="redpackTotal" id placeholder="请输入总金额" />
        <div class="hr"></div>
      </div>
      <div class="item">
        <label for>红包个数：</label>
        <input type="text" name="redpackNum" id placeholder="请输入红包个数" />
        <div class="hr"></div>
      </div>
      <div class="item">
        <label for>账户余额：</label>
        <input type="text" name="accountBalance" id placeholder="125.21" />
        <div class="hr"></div>
      </div>
      <div class="getRedpack">
        <div class="getredTag">下一个发红包：</div>
        <div class="minGet">
          <input type="radio" id="minlab" name="getRedpack" />
          <label for="minlab">抢到最少的发</label>
        </div>
        <div class="maxGet">
          <input type="radio" id="maxlab" name="getRedpack" />
          <label for="maxlab">抢到最多的发</label>
        </div>
      </div>
      <div class="setPassword getRedpack">
        <div class="getredTag">设置房间密码：</div>
        <div class="minGet">
          <input type="radio" id="yeslab" name="setPassword" />
          <label for="yeslab">是</label>
        </div>
        <div class="maxGet">
          <input type="radio" id="nolab" name="setPassword" />
          <label for="nolab">否</label>
        </div>
      </div>
      <input type="password" name="roomPassword" id placeholder="请输入您的房间密码" />
      <div class="hr hrPwd"></div>
      <p class="pwdTip">有密码的房间不会被推荐到首页，需要您自己一一邀请朋友加入</p>
      <div class="tipbox">
        <p>创群首发红包消耗：</p>
        <p>15EOS</p>
      </div>
      <p>1、其中一半的EOS作为群主首发红包的消耗，另一半的EOS作为群主押金，作为后面更新版本后的玩法押金。</p>
      <p>2、创群成功后，您的收益为群内每个哄好总额的3%。</p>
      <input type="submit" value="创建" />
    </form>
  </div>
</template>

<script>
export default {
  name: "RedpackGroup",
  data() {
    return {
      msg: "1111111111111111111"
    };
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='less' scoped>
.redpackGroup {
  width: 3.75rem;
  box-sizing: border-box;
  background-color: rgb(245, 245, 245);
  .hr {
    width: 3.6rem;
    margin-left: 0.08rem;
    margin-top: 0.08rem;
    height: 0.01rem;
    background: rgba(231, 231, 231, 1);
  }
  .title {
    width: 3.75rem;
    height: 0.44rem;
    background: rgba(57, 58, 62, 1);
    position: relative;
    img {
      width: 0.16rem;
      position: absolute;
      left: 0.17rem;
      bottom: 0.13rem;
      z-index: 2;
    }
    p {
      height: 0.16rem;
      font-weight: 500;
      color: rgba(255, 255, 255, 1);
      line-height: 0.15rem;
      transform: translateY(0.16rem);
    }
  }
  .createRedGroupForm {
    .item {
      background-color: #fff;
      padding-bottom: 0.1rem;
      label {
        display: block;
        text-align: left;
        width: 3.75rem;
        line-height: 0.36rem;
        padding-left: 0.15rem;
        box-sizing: border-box;
        color: rgba(34, 34, 34, 1);
      }
      input {
        font-size: 0.15rem;
        width: 3.75rem;
        line-height: 0.2rem;
        padding-left: 0.15rem;
        box-sizing: border-box;
        &::placeholder {
          color: rgba(173, 173, 173, 1);
        }
      }
    }
    .getRedpack {
      width: 3.75rem;
      height: 0.58rem;
      background-color: #fff;
      margin-top: 0.1rem;
      display: flex;
      .getredTag {
        font-size: 0.16rem;
        color: rgba(34, 34, 34, 1);
        width: 1.4rem;
        padding-top: 0.18rem;
        box-sizing: border-box;
      }
      .minGet {
        font-size: 0.12rem;
        color: rgba(153, 153, 153, 1);
        padding-top: 0.22rem;
        box-sizing: border-box;
        margin-right: 0.2rem;
      }
      .maxGet {
        font-size: 0.12rem;
        color: rgba(153, 153, 153, 1);
        padding-top: 0.22rem;
        box-sizing: border-box;
        margin-right: 0.2rem;
      }
      input[type="radio"] + label::before {
        content: "\a0"; /*不换行空格*/
        display: inline-block;
        vertical-align: middle;
        font-size: 18px;
        width: 0.12rem;
        height: 0.12rem;
        margin-right: 0.1rem;
        border-radius: 50%;
        border: 1px solid rgba(184, 184, 190, 1);
        text-indent: 0.15rem;
        line-height: 1;
        transform: translate(0rem, -0.02rem);
      }
      input[type="radio"]:checked + label::before {
        background-color: rgba(227, 60, 61, 1);
        border: 1px solid rgba(227, 60, 61, 1);
        width: 0.1rem;
        height: 0.1rem;
        background-clip: content-box;
        padding: 1px;
      }
      input[type="radio"]:checked + label {
        color: rgba(227, 60, 61, 1);
      }
      input[type="radio"] {
        position: absolute;
        clip: rect(0, 0, 0, 0);
      }
    }
    .setPassword {
      margin-top: 0;
    }
    input[type="password"] {
      width: 3.75rem;
      padding-left: 0.15rem;
      box-sizing: border-box;
      line-height: 0.3rem;
      background-color: #fff;
      font-size: 0.15rem;
      color: rgba(173, 173, 173, 1);
      transform: translateY(-0.15rem);
    }
    .hrPwd {
      transform: translateY(-0.25rem);
    }
    .pwdTip {
      width: 3.75rem;
      line-height: 0.3rem;
      font-size: 0.12rem;
      color: rgba(153, 153, 153, 1);
      background-color: #fff;
      transform: translateY(-0.25rem);
    }
    .tipbox {
      display: flex;
      height: 0.4rem;
      transform: translateY(-0.25rem);
      background-color: #fff;
      p:nth-child(1) {
        padding-left: 0.15rem;
        width: 1.5rem;
        line-height: 0.4rem;
        color: rgba(34, 34, 34, 1);
      }
      p:nth-child(2) {
        flex: 1;
        text-align: right;
        padding-right: 0.15rem;
        line-height: 0.4rem;
        font-size: 0.15rem;
        color: rgba(102, 102, 102, 1);
      }
    }
    p:nth-of-type(2),
    p:nth-of-type(3) {
      width: 3.75rem;
      text-align: left;
      padding: 0 0.15rem 0 0.15rem;
      transform: translateY(-0.25rem);
      box-sizing: border-box;
      height: 0.34rem;
      font-size: 0.12rem;
      background-color: #fff;
      color: rgba(102, 102, 102, 1);
      line-height: 0.16rem;
    }
    p:nth-of-type(3) {
      height: 0.3rem;
    }
    input[type="submit"] {
      margin: 0.2rem 0 0.2rem 0;
      width: 3.45rem;
      height: 0.45rem;
      background: rgba(227, 60, 61, 1);
      border-radius: 0.04rem;
      font-size: 0.17rem;
      color: rgba(245, 245, 245, 1);
      line-height: 0.15rem;
    }
  }
}
</style>



