<template>
  <div class="redEnvelopeGroup">
    <div class="title">
      <img src="../assets/btn_back_white@2x.png" alt onClick="window.history.back()" />
      <p>xxx币圈群(55)</p>
    </div>
    <div class="nav">
      <p>群成员在线人数:20人</p>
      <router-link to="/groupMembers">
        <span>...</span>
      </router-link>
    </div>
    <div class="content">
      <ul>
        <li>
          <img src="../assets/image_fang @2x.png" alt />
          <div @click="openRed">
            <img src="../assets/image_hongbao_normal@2x.png" alt />
            <p>恭喜发财，大吉大利</p>
            <p>5EOS 6包</p>
            <p>抢到最小的发</p>
          </div>
        </li>
        <li class="self">
          <img src="../assets/image_fang @2x.png" alt />
          <div @click="openNotRed">
            <img src="../assets/image_hongbao_click@2x.png" alt />
            <p>恭喜发财，大吉大利</p>
            <p>5EOS 6包</p>
            <p>抢到最小的发</p>
          </div>
          <div class="clickResult">
            <p>你抢了xxx个xx币</p>
            <p>红包已抢完，耗时xx秒</p>
            <p>下一个红包将由xx发出</p>
          </div>
        </li>
      </ul>
    </div>
    <ul class="fixed">
      <li class="icon">
        <img src="../assets/image_yuan@2x.png" alt />
      </li>
      <li class="collection">
        <img src="../assets/btn_like_normal@2x.png" alt />
        <p>212</p>
      </li>
      <li class="comment">
        <img src="../assets/SPEECH 3@2x.png" alt @click="openComment" />
        <p>212</p>
      </li>
      <li class="share">
        <router-link to="inviteFriendGrabRedpack">
          <img src="../assets/share 4@2x.png" alt />
          <p>212</p>
        </router-link>
      </li>
      <li class="next">
        <img src="../assets/btn_next@2x.png" alt />
        <p>下一个</p>
      </li>
    </ul>
    <div class="popup" v-show="isPopup">
      <router-link to="/redEnvelopeDetails">
        <img src="../assets/image_redpacket_normal@2x.png" alt class="big" />
        <p>
          <img src="../assets/image_yuan@2x.png" alt />
          小小的红包
        </p>
        <p>恭喜发财，大吉大利</p>
      </router-link>
      <img src="../assets/btn_close_yellow@2x.png" alt class="off" @click="handerOff" />
    </div>
    <div class="popup" v-show="isNotRedPopup">
      <img src="../assets/notred 10@2x.png" alt class="big" />
      <p>
        <img src="../assets/image_yuan@2x.png" alt />
        小小的红包
      </p>
      <p>手慢了，红包派完了</p>
      <div class="seeDetail" @click="handerSee">
        <p>查看领取详情</p>
        <img src="../assets/btn_more_yellow@2x.png" alt />
      </div>
      <img src="../assets/btn_close_yellow@2x.png" alt class="off" @click="handerOff" />
    </div>
    <div class="msePopup" v-show="isComment">
      <div class="title">
        <p>32条评论</p>
        <img src="../assets/btn_clear_gray@2x.png" alt @click="offComment" />
      </div>
      <ul class="content">
        <li>
          <img src="../assets/image_yuan@2x.png" alt />
          <p>一起来摇摆</p>
          <p>我好了，你们呢</p>
          <p>7-3</p>
          <p>
            1&nbsp;
            <img src="../assets/btn_good_normal@2x.png" alt />
            <span>回复</span>
          </p>
        </li>
        <li class="hr"></li>
      </ul>
      <input type="text" placeholder="请输入您的精彩评论吧" />
    </div>
  </div>
</template>

<script>
export default {
  name: "RedEnvelopeGroup",
  data() {
    return {
      isPopup: false,
      isNotRedPopup: false,
      isComment: false
    };
  },
  methods: {
    openRed() {
      this.isPopup = true;
    },
    openNotRed() {
      this.isNotRedPopup = true;
    },
    handerOff() {
      this.isPopup = false;
      this.isNotRedPopup = false;
    },
    openComment() {
      this.isComment = true;
    },
    offComment() {
      this.isComment = false;
    },
    handerSee() {
      this.$router.push("redEnvelopeDetails");
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='less' scoped>
.redEnvelopeGroup {
  height: 100vh;
  box-sizing: border-box;
  background-color: rgb(245, 245, 245);
  display: flex;
  flex-direction: column;
  .title {
    width: 3.75rem;
    height: 0.44rem;
    background: rgba(57, 58, 62, 1);
    position: relative;
    img {
      width: 0.16rem;
      position: absolute;
      left: 0.17rem;
      bottom: 0.13rem;
    }
    p {
      height: 0.16rem;
      font-weight: 500;
      color: rgba(255, 255, 255, 1);
      line-height: 0.15rem;
      margin-top: 0.15rem;
    }
  }
  .nav {
    width: 3.75rem;
    height: 0.4rem;
    background: rgba(237, 237, 237, 1);
    position: relative;
    p {
      height: 0.13rem;
      font-size: 0.14rem;
      color: rgba(64, 64, 64, 1);
      line-height: 0.15rem;
      position: absolute;
      left: 0.15rem;
      top: 0.14rem;
    }
    span {
      width: 0.13rem;
      height: 0.13rem;
      font-size: 0.14rem;
      color: rgba(64, 64, 64, 1);
      line-height: 0.15rem;
      position: absolute;
      right: 0.15rem;
      top: 0.09rem;
    }
  }
  .content {
    width: 100%;
    flex: 1;
    li {
      position: relative;
      width: 3.75rem;
      height: 0.8rem;
      margin-top: 0.1rem;
      img {
        width: 0.35rem;
        position: absolute;
        left: 0.15rem;
      }
      div {
        img {
          width: 2.36rem;
          position: absolute;
          left: 0.57rem;
        }
        p {
          height: 0.16rem;
          color: rgba(255, 255, 255, 1);
          line-height: 0.15rem;
          position: absolute;
          left: 1.17rem;
          top: 0.11rem;
        }
        p:nth-child(3) {
          height: 0.11rem;
          font-size: 0.12rem;
          color: rgba(255, 255, 255, 1);
          line-height: 0.15rem;
          position: absolute;
          left: 1.17rem;
          top: 0.35rem;
        }
        p:nth-child(4) {
          height: 0.11rem;
          font-size: 0.11rem;
          color: rgba(145, 145, 145, 1);
          line-height: 0.15rem;
          position: absolute;
          left: 0.75rem;
          top: 0.64rem;
        }
      }
    }
    li.self {
      position: relative;
      width: 3.75rem;
      height: 0.8rem;
      img {
        width: 0.35rem;
        position: absolute;
        left: 3rem;
      }
      div {
        img {
          width: 2.36rem;
          position: absolute;
          left: 0.57rem;
        }
      }
      .clickResult {
        position: absolute;
        width: 3.75rem;
        height: 0.49rem;
        top: 0.9rem;
        p {
          font-size: 0.11rem;
          color: rgba(145, 145, 145, 1);
          line-height: 0.19rem;
          position: static;
        }
        p:nth-child(1) {
          color: #fa9d3b;
        }
      }
    }
  }
  .fixed {
    position: fixed;
    right: 0.2rem;
    top: 2rem;
    li {
      position: relative;
      margin-top: 0.2rem;
      height: 0.44rem;
      width: 0.5rem;
    }
    img {
      width: 0.24rem;
    }
    p {
      position: absolute;
      width: 100%;
      bottom: 0;
      height: 0.2rem;
      font-size: 0.12rem;
      color: rgba(102, 102, 102, 1);
      line-height: 0.2rem;
    }
    .icon img {
      width: 0.35rem;
      height: 0.35rem;
      background: rgba(135, 135, 135, 1);
      border-radius: 50%;
    }
    .next {
      margin-top: 0.6rem;
    }
  }
  .popup {
    position: fixed;
    width: 100vw;
    height: 100vh;
    background-color: rgba(146, 146, 146, 0.37);
    .seeDetail {
      p {
        width: 1rem;
        height: 0.13rem;
        font-size: 0.14rem;
        color: rgba(255, 247, 193, 1);
        line-height: 0.15rem;
        transform: translate(1.39rem, 4.2rem);
      }
      img {
        height: 0.1rem;
        transform: translate(0.5rem, 4.07rem);
      }
    }
    .big {
      position: absolute;
      left: 0.41rem;
      top: 0.54rem;
      width: 2.94rem;
      height: 4.79rem;
    }
    .off {
      position: absolute;
      left: 1.7rem;
      top: 5.7rem;
      width: 0.36rem;
      height: 0.36rem;
    }
    p {
      transform: translateY(1.7rem);
      font-size: 0.17rem;
      font-weight: 500;
      color: rgba(255, 236, 184, 1);
      line-height: 0.2rem;
      img {
        transform: translateY(0.045rem);
        width: 0.22rem;
        height: 0.22rem;
      }
    }
    p:nth-child(3) {
      transform: translateY(1.7rem);
      font-size: 0.22rem;
      font-weight: 500;
      color: rgba(255, 243, 190, 1);
      line-height: 0.55rem;
    }
  }
  .msePopup {
    width: 3.75rem;
    height: 4.5rem;
    background: rgba(255, 255, 255, 1);
    border-radius: 0.1rem 0.1rem 0rem 0rem;
    position: fixed;
    left: 0;
    bottom: 0;
    .title {
      width: 3.75rem;
      height: 0.36rem;
      background: rgb(255, 255, 255);
      padding-top: 0;
      p {
        margin-top: 0;
        height: 0.14rem;
        font-size: 0.14rem;
        color: rgba(0, 0, 0, 1);
        line-height: 0.36rem;
      }
      img {
        float: right;
        position: static;
        margin-right: 0.14rem;
      }
    }
    .content {
      width: 3.75rem;
      li {
        height: 0.9rem;
        margin-top: 0;
        img {
          top: 0.15rem;
        }
        p {
          width: 1.3rem;
          height: 0.13rem;
          font-size: 0.13rem;
          color: rgba(0, 0, 0, 1);
          line-height: 0.21rem;
          text-align: left;
          margin-left: 0.65rem;
          padding-top: 0.15rem;
          transform: translate(0, -0.05rem);
        }
        p:nth-child(3) {
          color: rgba(51, 51, 51, 1);
          line-height: 0.21rem;
          transform: translate(0, -0.09rem);
        }
        p:nth-child(4) {
          color: rgba(129, 129, 129, 1);
          line-height: 0.21rem;
          transform: translate(0, -0.09rem);
        }
        p:nth-child(5) {
          text-align: right;
          padding-right: 0.3rem;
          float: right;
          color: rgba(129, 129, 129, 1);
          line-height: 0.21rem;
          transform: translate(0, -0.34rem);
          img {
            width: 0.11rem;
            position: static;
          }
          span {
            padding-left: 0.2rem;
          }
        }
      }
      .hr {
        width: 3.75rem;
        height: 0.01rem;
        background: rgba(239, 239, 239, 1);
      }
    }
    input {
      padding-left: 0.1rem;
      width: 3.75rem;
      height: 0.44rem;
      position: absolute;
      left: 0;
      bottom: 0;
      border: none;
      border-top: rgba(239, 239, 239, 1) solid 1px;
    }
  }
}
</style>



