<template>
  <div class="currencyRoom">
    <div class="title">
      <img src="../../assets/btn_back_white@2x.png" alt onClick='window.history.back()'/>
      <p>BTC区红包接龙</p>
    </div>
    <ul class="list">
      <li>
        <img src="../../assets/icon_bizhong@2x.png" alt />
        <p>Xx个币xx个包</p>
        <p>在线人员数25</p>
        <span>最大/最小的发</span>
        <router-link to="/redEnvelopeGroup">
          <button>前往</button>
        </router-link>
      </li>
      <div class="hr"></div>
    </ul>
  </div>
</template>

<script>
export default {
  name: "CurrencyRoom",
  data() {
    return {
    };
  },
  methods: {
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='less' scoped>
.currencyRoom {
  height: 100vh;
  box-sizing: border-box;
  background-color: rgb(245, 245, 245);
  position: relative;
  .title {
    width: 3.75rem;
    height: 0.44rem;
    background: rgba(57, 58, 62, 1);
    position: relative;
    img {
      width: 0.16rem;
      position: absolute;
      left: 0.17rem;
      bottom: 0.13rem;
      z-index: 2;
    }
    p {
      height: 0.16rem;
      font-weight: 500;
      color: rgba(255, 255, 255, 1);
      line-height: 0.15rem;
      transform: translateY(0.16rem)
    }
  }
  .list {
    width: 3.75rem;
    background: rgba(255, 255, 255, 1);
    .hr {
      width: 3.45rem;
      height: 0.01rem;
      background: rgba(247, 247, 247, 1);
      margin-left: 0.15rem;
    }
    li {
      width: 3.75rem;
      height: 0.73rem;
      background: rgba(255, 255, 255, 1);
      position: relative;
      img {
        width: 0.4rem;
        position: absolute;
        left: 0.15rem;
        bottom: 0.17rem;
      }
      p {
        height: 0.13rem;
        font-size: 0.14rem;
        color: rgba(64, 64, 64, 1);
        line-height: 0.08rem;
        position: absolute;
        left: 0.65rem;
        top: 0.21rem;
      }
      p:nth-child(3) {
        height: 0.12rem;
        font-size: 0.12rem;
        color: rgba(64, 64, 64, 1);
        line-height: 0.08rem;
        position: absolute;
        left: 2.87rem;
        top: 0.21rem;
      }
      span {
        height: 0.1rem;
        font-size: 0.1rem;
        color: rgba(153, 153, 153, 1);
        line-height: 0.08rem;
        position: absolute;
        left: 0.66rem;
        top: 0.41rem;
      }
      button {
        width: 0.32rem;
        height: 0.17rem;
        background: #FABD69;
        font-size: 0.12rem;
        color: rgba(255, 255, 255, 1);
        line-height: 0.17rem;
        border-radius: 0.02rem;
        position: absolute;
        right: 0.15rem;
        top: 0.38rem;
      }
    }
  }
}
</style>



