<template>
  <div class="fortuneTree">
    <div class="title">
      <img src="../../assets/btn_back_white@2x.png" alt onClick='window.history.back()'/>
      <p>免费创建财富树</p>
    </div>
    <form action class="createFortuneTreeForm">
      <div class="item">
        <label for>选择财富树币种区:</label>
        <input type="text" name="currencyName" id placeholder="请输入币种名称" />
        <button>切换币种</button>
        <div class="hr"></div>
      </div>
      <div class="rank">
        <p>层级:</p>
        <div class="number">
          <img src="../../assets/icon_cut@2x.png" alt @click="reduce" />
          <span>{{rankNum}}</span>
          <img src="../../assets/icon_add@2x.png" alt @click="add" />
        </div>
      </div>
      <div class="hr" style="margin-top: 0"></div>
      <div class="item">
        <label for>第一层金额:</label>
        <input type="text" name="rankOne" id placeholder="xxxxEOS" />
        <div class="hr"></div>
      </div>
      <div class="rank increaseRate">
        <p>每层金额递增率:</p>
        <div class="number">
          <img src="../../assets/icon_cut@2x.png" alt @click="reduceRate" />
          <span>{{rankRateNum}}%</span>
          <img src="../../assets/icon_add@2x.png" alt @click="addRate" />
        </div>
      </div>
      <!-- multiplexing -->
      <div class="getRedpack">
        <div class="getredTag">推广限制：</div>
        <div class="minGet">
          <input type="radio" id="minlab" name="extension" />
          <label for="minlab">
            <div class="number">
              <img src="../../assets/icon_cut@2x.png" alt @click="reduceExtension" />
              <span>一推{{extensionNum}}</span>
              <img src="../../assets/icon_add@2x.png" alt @click="addExtension" />
            </div>
          </label>
        </div>
        <div class="maxGet">
          <input type="radio" id="maxlab" name="extension" />
          <label for="maxlab">一推无数</label>
        </div>
      </div>
      <div class="hr limitUnder"></div>
      <p>创建此财富树需要缴纳xx个币的押金，创立成功后，您的位置属于金字塔顶端。</p>
      <p>押金退还机制：您创立的财富树收入超过押金金额后，可以申请退还您的押金。</p>
      <input type="submit" value="创建" />
    </form>
  </div>
</template>

<script>
export default {
  name: "FortuneTree",
  data() {
    return {
      rankNum: 1,
      rankRateNum: 1,
      extensionNum: 2,
    };
  },
  methods: {
    reduce() {
      this.rankNum -= 1;
    },
    reduceRate() {
      this.rankRateNum -= 1;
    },
    reduceExtension() {
      this.extensionNum -= 1;
    },
    add() {
      this.rankNum += 1;
    },
    addRate() {
      this.rankRateNum += 1;
    },
    addExtension() {
      this.extensionNum += 1;
    },
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='less' scoped>
.fortuneTree {
  width: 3.75rem;
  box-sizing: border-box;
  background-color: rgb(245, 245, 245);
  min-height: 100vh;
  overflow: hidden;
  .hr {
    width: 3.6rem;
    margin-left: 0.08rem;
    margin-top: 0.08rem;
    height: 0.01rem;
    background: rgba(231, 231, 231, 1);
  }
  .title {
    width: 3.75rem;
    height: 0.44rem;
    background: rgba(57, 58, 62, 1);
    position: relative;
    img {
      width: 0.16rem;
      position: absolute;
      left: 0.17rem;
      bottom: 0.14rem;
      z-index: 2;
    }
    p {
      height: 0.16rem;
      font-weight: 500;
      color: rgba(255, 255, 255, 1);
      line-height: 0.15rem;
      transform: translateY(0.15rem);
    }
  }
  .createFortuneTreeForm {
    .number {
      display: flex;
      flex: 1;
      justify-content: flex-end;
      align-items: center;
      padding-right: 0.15rem;
      img {
        width: 0.18rem;
        height: 0.18rem;
      }
      span {
        width: 0.5rem;
      }
    }
    .item {
      background-color: #fff;
      label {
        display: block;
        text-align: left;
        width: 3.75rem;
        line-height: 0.36rem;
        padding-left: 0.15rem;
        box-sizing: border-box;
        color: rgba(34, 34, 34, 1);
      }
      input {
        font-size: 0.15rem;
        width: 3.75rem;
        line-height: 0.2rem;
        padding-left: 0.15rem;
        box-sizing: border-box;
        &::placeholder {
          color: rgba(173, 173, 173, 1);
        }
      }
      button {
        width: 0.63rem;
        line-height: 0.2rem;
        float: right;
        margin-right: 0.15rem;
        margin-top: -0.19rem;
        background: rgba(227, 60, 61, 1);
        border-radius: 0.04rem;
        font-size: 0.13rem;
        color: rgba(255, 255, 255, 1);
      }
    }
    .rank {
      background-color: #fff;
      display: flex;
      width: 3.75rem;
      p {
        flex: 1;
        line-height: 0.58rem;
        text-align: left;
        padding-left: 0.15rem;
        color: rgba(34, 34, 34, 1);
      }
    }
    .increaseRate {
      flex-direction: column-reverse;
      align-items: flex-start;
      p {
        line-height: 0.13rem;
        font-size: 0.11rem;
        color: rgba(173, 173, 173, 1);
        margin-bottom: 0.1rem;
      }
      .number {
        padding-left: 0.15rem;
        line-height: 0.3rem;
      }
    }
    .getRedpack {
      width: 3.75rem;
      height: 1.1rem;
      background-color: #fff;
      margin-top: 0.1rem;
      display: flex;
      flex-direction: column;
      .getredTag {
        font-size: 0.16rem;
        color: rgba(34, 34, 34, 1);
        width: 1.4rem;
        padding-top: 0.1rem;
        text-align: left;
        padding-left: 0.15rem;
      }
      .minGet {
        font-size: 0.12rem;
        color: rgba(153, 153, 153, 1);
        padding-top: 0.15rem;
        display: flex;
        label {
          margin-left: 0.15rem;
          display: flex;
          align-items: center;
          .number {
            margin-top: -0.05rem;
          }
        }
      }
      .maxGet {
        font-size: 0.12rem;
        color: rgba(153, 153, 153, 1);
        padding-top: 0.15rem;
        display: flex;
        label {
          margin-left: 0.15rem;
        }
      }
      input[type="radio"] + label::before {
        content: "\a0"; /*不换行空格*/
        display: inline-block;
        vertical-align: middle;
        font-size: 18px;
        width: 0.12rem;
        height: 0.12rem;
        margin-right: 0.1rem;
        border-radius: 50%;
        border: 1px solid rgba(184, 184, 190, 1);
        text-indent: 0.15rem;
        line-height: 1;
        transform: translate(0rem, -0.02rem);
      }
      input[type="radio"]:checked + label::before {
        background-color: rgba(227, 60, 61, 1);
        border: 1px solid rgba(227, 60, 61, 1);
        width: 0.1rem;
        height: 0.1rem;
        background-clip: content-box;
        padding: 1px;
      }
      input[type="radio"]:checked + label {
        color: rgba(227, 60, 61, 1);
      }
      input[type="radio"] {
        position: absolute;
        clip: rect(0, 0, 0, 0);
      }
    }
    .limitUnder {
      margin-top: 0;
    }
    p:nth-of-type(1),
    p:nth-of-type(2) {
      width: 3.45rem;
      text-align: left;
      padding: 0.15rem 0.15rem 0 0.15rem;
      transform: translateY(-0.2rem);
      box-sizing: content-box;
      height: 0.34rem;
      font-size: 0.12rem;
      background-color: #fff;
      color: rgba(102, 102, 102, 1);
      line-height: 0.16rem;
      margin-top: 0.2rem;
    }
    p:nth-of-type(2) {
      height: 0.5rem;
      margin-top: 0;
      padding-top: 0;
    }
    input[type="submit"] {
      margin: 0.2rem 0 0.2rem 0;
      width: 3.45rem;
      height: 0.45rem;
      background: rgba(227, 60, 61, 1);
      border-radius: 0.04rem;
      font-size: 0.17rem;
      color: rgba(245, 245, 245, 1);
      line-height: 0.15rem;
    }
  }
}
</style>



