<template>
  <div class="mi">
    <div class="head">
      <p class="title">我的</p>
      <img :src="iconSrc" alt @click="tapIcon" />
      <p class="desc">{{nickName}}</p>
    </div>
    <ul class="list">
      <li>
        <router-link to="CurrencyTypes">
          <img src="../assets/per_icon_zijin@2x.png" alt />
          <p>个人资金</p>
          <i></i>
        </router-link>
      </li>
      <li>
        <router-link to="redTotalincome">
          <img src="../assets/per_icon_hbsy@2x.png" alt />
          <p>红包总收益</p>
          <i></i>
        </router-link>
      </li>
      <li>
        <router-link to="freetreeTotalincome">
          <img src="../assets/per_icon_cfsy@2x.png" alt />
          <p>财富自由之树总收益</p>
          <i></i>
        </router-link>
      </li>
      <li>
        <router-link :to="{path:'signonSuccess', query:{ title: '备份账户密码', isShowBtn: false }}">
          <img src="../assets/per_icon_password@2x.png" alt />
          <p>备份账户密码</p>
          <i></i>
        </router-link>
      </li>
      <li>
        <router-link to="wechatBind">
          <img src="../assets/per_icon_code@2x.png" alt />
          <p>上传微信二维码</p>
          <i></i>
        </router-link>
      </li>
      <li @click="tapItem" index="6">
        <a href="javascript:void(0);">
          <img src="../assets/per_icon_friend@2x.png" alt />
          <p>邀请好友</p>
        </a>
        <i></i>
      </li>
      <li>
        <router-link to="feedback">
          <img src="../assets/per_icon_feedback@2x.png" alt />
          <p>意见反馈</p>
          <i></i>
        </router-link>
      </li>
      <li>
        <router-link to="ruleNotice">
          <img src="../assets/per_icon_notice@2x.png" alt />
          <p>规则与公告</p>
          <i></i>
        </router-link>
      </li>
    </ul>
    <button @click="tapQuit" v-show="isQuit">退出登录</button>
  </div>
</template>

<script>
export default {
  name: "Mi",
  data() {
    return {
      iconSrc: "../../static/per_image_default@2x.png",
      nickName: "登录后体验更多服务",
      isQuit: false
    };
  },
  methods: {
    tapIcon() {
      if (this.$store.state.isLogin === false) {
        this.$router.push("signin");
      } else {
        this.$router.push("personalInformation");
      }
    },
    tapQuit() {
      // this.$axios
      //   .post("user/logout")
      //   .then(res => {
      //     const data = res.data.data;
      //     if (res.data.status === 1) {
      //       this.$store.commit("handerQuit");
      //       this.handerLogin();
      //     } else {
      //       this.$toast("退出失败");
      //     }
      //   })
      //   .catch(err => {
      //     this.$toast("访问地址有误");
      //   });
      debugger
      console.log(this.$store.state.token)
      console.log(this.$store.state.sessionid)
      this.$axios({
        method: "post",
        url: "user/logout",
        headers: {
          token: this.$store.state.token,
          sessionid: this.$store.state.sessionid,
        }
      })
        .then(res => {
          const data = res.data.data;
          if (res.data.status === 1) {
            this.$store.commit("handerQuit");
            this.handerLogin();
          } else {
            this.$toast("退出失败");
          }
        })
        .catch(err => {
          this.$toast("访问地址有误");
        });
    },
    handerLogin() {
      if (this.$store.state.isLogin) {
        this.iconSrc = "../../static/image_yuan@2x.png";
        this.nickName = this.$route.query.username;
        this.isQuit = true;
      } else {
        this.iconSrc = "../../static/per_image_default@2x.png";
        this.nickName = "登录后体验更多服务";
        this.isQuit = false;
      }
    },
    tapItem(e) {
      const el = e.currentTarget;
      const elIndex = el.getAttribute("index");
      const isLogin = this.$store.state.isLogin;
      if (elIndex === "6") {
        if (isLogin === false) {
          this.$router.push("signin");
        } else {
          this.$router.push("inviteFriend");
        }
      }
    }
  },
  beforeMount() {
    this.handerLogin();
  },
  mounted() {
    this.$parent.handMi();
  }
};
</script>

<style lang='less' scoped>
  .mi {
    min-height: ~'calc(100vh - 0.48rem)';
    padding-bottom: 0.2rem;
    box-sizing: border-box;
    background-color: #F5F5F5;
    .head {
      width: 3.75rem;
      height: 1.45rem;
      background: rgba(227, 60, 61, 1);
      padding: 0.17rem 0 0.11rem;
      box-sizing: border-box;
      position: relative;
      .title {
        font-weight: 500;
        color: rgba(255, 255, 255, 1);
        line-height: 0.15rem;
      }
      img {
        width: 0.59rem;
        position: absolute;
        left: 0.15rem;
        bottom: 0.11rem;
      }
      .desc {
        font-size: 0.18rem;
        color: rgba(242, 184, 179, 1);
        position: absolute;
        left: 0.84rem;
        bottom: 0.3rem;
      }
    }
    .list {
      li {
        width: 100%;
        height: 0.46rem;
        background: rgba(255, 255, 255, 1);
        position: relative;
        margin-bottom: .01rem;
        a{
          display: block;
          height: 100%;
          margin-left: .15rem;
        }
        img {
          width: 0.2rem;
          position: absolute;
          left: 0.15rem;
          bottom: 0.13rem;
        }
        p {
          height: 0.46rem;
          font-size: 0.14rem;
          color: #3f3f3f;
          line-height: 0.48rem;
          text-align: left;
          margin-left: .35rem;
        }
        i {
          background: url("../assets/btn_more_gray@2x.png");
          background-size: contain;
          width: 0.12rem;
          height: 0.12rem;
          display: block;
          position: absolute;
          right: 0.13rem;
          bottom: 0.17rem;
        }
      }
      li:nth-child(3),
      li:nth-child(5) {
        margin-bottom: 0.1rem;
      }
    }
    button {
      font-size: 0.17rem;
      color: rgba(245, 245, 245, 1);
      width: 3.45rem;
      height: 0.45rem;
      background: rgba(227, 60, 61, 1);
      border-radius: 0.04rem;
      margin-top: 0.2rem;
    }
  }
</style>



