<template>
  <div class="fortuneTreeDetail">
    <div class="header">
      <div class="title">
        <i onClick="window.history.back()"></i>
        <p>财富自由之树升级</p>
      </div>
      <p>Ⅰ级</p>
    </div>
    <div class="popup">
      <p>USDT余额:55SDT</p>
      <p>升级到</p>
      <div>
        <img src="../../assets/shengji_image@2x.png" alt />
        <img src="../../assets/plus_icon@2x.png" alt />
      </div>
      <div>
        <span>升到2级需要缴纳:10USDT</span>
        <button>立即升级</button>
      </div>
    </div>
    <div class="fixed">
      <ul>
        <li>
          <img src="../../../static/image_yuan@2x.png" alt />
          <p></p>
        </li>
        <li>
          <router-link to="fortuneTreeDetail">
            <img src="../../../static/rocket_icon_click@2x.png" alt />
            <p>升级</p>
          </router-link>
        </li>
        <li>
          <router-link to="fortuneTreeTeamManager">
            <img src="../../../static/team_icon_normal@2x.png" alt />
            <p>团队</p>
          </router-link>
        </li>
        <li>
          <router-link to="fortuneTreeInvitation">
            <img src="../../../static/addfriend_icon_normal@2x.png" alt />
            <p>邀请</p>
          </router-link>
        </li>
        <li>
          <router-link to="fortuneDetail">
            <img src="../../../static/calculator_icon_normal@2x.png" alt />
            <p>财务</p>
          </router-link>
        </li>
        <li>
          <router-link to="fortuneTreeDetailMessage">
            <img src="../../../static/news_icon_normal@2x.png" alt />
            <p>信息</p>
          </router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "FortuneTreeDetail"
};
</script>

<style lang='less' scoped>
.fortuneTreeDetail {
  .header {
    width: 3.75rem;
    height: 2.07rem;
    background: url("../../../static/medal_background@2x.png");
    background-size: cover;
    .title {
      overflow: hidden;
      position: relative;
      p {
        font-weight: 500;
        color: rgba(255, 255, 255, 1);
        margin-top: 0.14rem;
      }
      i {
        background: url("../../../static/btn_back_white@2x.png");
        background-size: cover;
        width: 0.16rem;
        height: 0.16rem;
        position: absolute;
        left: 0.17rem;
        top: 0.18rem;
      }
    }
    p {
      font-size: 0.17rem;
      color: rgba(35, 35, 35, 1);
      margin-top: 1.6rem;
    }
  }
  .popup {
    width: 2.45rem;
    height: 1.52rem;
    margin: auto;
    margin-top: 0.6rem;
    overflow: hidden;
    box-shadow: 0 0 9px 1px rgba(202, 202, 202, 0.603);
    p:nth-child(1) {
      font-size: 0.15rem;
      color: rgba(34, 34, 34, 1);
      margin-top: 0.27rem;
    }
    p:nth-child(2) {
      font-size: 0.14rem;
      color: rgba(227, 60, 61, 1);
      margin-top: 0.1rem;
    }
    div:nth-child(3) {
      img:nth-child(1) {
        width: 1.61rem;
        height: 0.1rem;
        margin-right: 0.1rem;
        transform: translateY(-0.04rem);
      }
      img:nth-child(2) {
        width: 0.18rem;
        height: 0.18rem;
      }
    }
    div:nth-child(4) {
      margin-top: 0.2rem;
      span {
        font-size: 0.11rem;
        color: rgba(64, 64, 64, 1);
        transform: translateY(-0.04rem);
      }
      button {
        font-size: 0.13rem;
        color: rgba(255, 255, 255, 1);
        width: 0.63rem;
        height: 0.2rem;
        background: rgba(253, 164, 24, 1);
        border-radius: 0.03rem;
      }
    }
  }
  .fixed {
    position: fixed;
    right: 0.21rem;
    top: 2.23rem;
    ul {
      li {
        margin-bottom: 0.2rem;
        img {
          width: 0.24rem;
          height: 0.24rem;
        }
        p {
          font-size: 0.12rem;
          color: rgba(34, 34, 34, 1);
        }
      }
      li:nth-child(1) {
        img {
          width: 0.35rem;
          height: 0.35rem;
        }
      }
    }
  }
}
</style>



