<template>
  <div class="redEnvelopeDetails">
    <div class="title">
      <router-link to="/redEnvelopeGroup">
        <img src="../../assets/btn_back_white@2x.png" alt />
      </router-link>
      <p>
        <img src="../../assets/image_yuan@2x.png" alt />
        小小的红包
      </p>
      <p>恭喜发财，大吉大利</p>
    </div>
    <p class="header">最少 / 下一个红包将由您发出</p>
    <ul class="list">
      <div class="hr"></div>
      <li>
        <img src="../../assets/image_yuan@2x.png" alt />
        <p>
          橙子
          <span>游客</span>
        </p>
        <p>2EOS</p>
        <span>08-04 09:32</span>
        <button>最少</button>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "RedEnvelopeDetails",
  data() {
    return {};
  },
  methods: {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='less' scoped>
.redEnvelopeDetails {
  height: 100vh;
  .title {
    height: 1.92rem;
    background: url("../../../static/bg_redpacket@2x.png");
    background-size: contain;
    position: relative;
    a {
      img {
        width: 0.16rem;
        position: absolute;
        left: 0.2rem;
        top: 0.15rem;
      }
    }
    p {
      transform: translateY(0.87rem);
      font-size: 0.17rem;
      font-weight: 500;
      color: rgba(255, 236, 184, 1);
      line-height: 0.2rem;
      img {
        transform: translateY(0.045rem);
        width: 0.22rem;
        height: 0.22rem;
      }
    }
    p:nth-child(3) {
      font-size: 0.13rem;
      color: rgba(255, 243, 190, 1);
      line-height: 0.4rem;
    }
  }
  .header {
    height: 0.7rem;
    font-weight: 500;
    color: rgba(227, 60, 61, 1);
    line-height: 1rem;
  }
  .list {
    width: 3.75rem;
    background: rgba(255, 255, 255, 1);
    margin-top: 0.1rem;
    .hr {
      width: 3.45rem;
      height: 0.01rem;
      background: rgba(247, 247, 247, 1);
      margin-left: 0.15rem;
    }
    li {
      width: 3.75rem;
      height: 0.73rem;
      background: rgba(255, 255, 255, 1);
      position: relative;
      img {
        width: 0.4rem;
