        height: 0.13rem;
        font-size: 0.14rem;
        color: rgba(64, 64, 64, 1);
        line-height: 0.08rem;
        position: absolute;
        left: 0.65rem;
        top: 0.21rem;
        span {
          position: absolute;
          left: 0.4rem;
          top: -0.04rem;
          display: block;
          width: 0.33rem;
          height: 0.18rem;
          background: rgba(244, 85, 64, 1);
          border-radius: 0.03rem;
          font-size: 0.11rem;
          color: rgba(252, 252, 252, 1);
          line-height: 0.18rem;
        }
      }
      p:nth-child(3) {
        height: 0.12rem;
        font-size: 0.12rem;
        color: rgba(64, 64, 64, 1);
        line-height: 0.08rem;
        position: absolute;
        width: 2.94rem;
        text-align: right;
        top: 0.21rem;
      }
      span {
        height: 0.1rem;
        font-size: 0.1rem;
        color: rgba(153, 153, 153, 1);
        line-height: 0.08rem;
        position: absolute;
        left: 0.66rem;
        top: 0.41rem;
      }
      button {
        width: 0.32rem;
        height: 0.17rem;
        background: rgba(250, 189, 105, 1);
        font-size: 0.12rem;
        color: rgba(255, 255, 255, 1);
        line-height: 0.17rem;
        border-radius: 0.02rem;
        position: absolute;
        right: 0.15rem;
        top: 0.38rem;
      }
    }
  }
}
</style>



