<template>
  <div class="personalFunds">
    <div class="banner">
      <div class="header">
        <div class="title">
          <i onClick='window.history.back()'></i>
          <p>USDT</p>
        </div>
      </div>
      <img src="../../../static/usd_logo@2x.png" alt class="icon" />
      <p class="topMain">USDT</p>
      <p class="center">Tether</p>
      <div class="bottomMain">
        <hr style="width:0.96rem; height:0; border:0.5px solid rgba(132,136,140,1);" />
        <span>持有数</span>
        <hr style="width:0.96rem; height:0; border:0.5px solid rgba(132,136,140,1);" />
      </div>
      <p class="num">0</p>
      <div class="content">
        <div class="left">
          <p class="top">可用余额</p>
          <p class="bottom">5210</p>
        </div>
        <div class="right">
          <p class="top">冻结金额</p>
          <p class="bottom">5210</p>
        </div>
      </div>
      <div class="footer">
        <router-link to="fillingupRecords">
          <img src="../../../static/btn_cwjl@2x.png" alt />
          <span>财务记录</span>
        </router-link>
      </div>
    </div>
    <p class="mainTip">充值地址</p>
    <div class="erBox">
      <img src="../../../static/erweima_image@2x.png" alt class="mainEr" />
    </div>
    <div class="erCode">
      <span id='erCode'>adve2egs22c58exd58e5x47e6xg5yj2k7sxv</span>
      <img src="../../../static/btn_copy_red@2x.png" alt @click="copyErcode"/>
    </div>
    <router-link to="signon">
      <button>提现</button>
    </router-link>
  </div>
</template>

<script>
import { copyFun } from '../../assets/js/unit';
export default {
  name: "PersonalFunds",
  data() {
    return {};
  },
  methods: {
    copyErcode(){
      copyFun('erCode')
    }
  }
};
</script>

<style lang='less' scoped>
.personalFunds {
  background-color: rgb(248, 248, 248);
  height: 100vh;
  .banner {
    height: 3.25rem;
    background: url("../../../static/black_bg@2x.png");
    background-size: cover;
    position: relative;
    .header {
      width: 3.75rem;
      height: 0.64rem;
      .title {
        overflow: hidden;
        position: relative;
        height: 0.44rem;
        p {
          font-weight: 500;
          color: rgba(255, 255, 255, 1);
          line-height: 0.15rem;
          margin-top: 0.15rem;
        }
        i {
          background: url("../../../static/btn_back_white@2x.png");
          background-size: cover;
          width: 0.16rem;
          height: 0.16rem;
          position: absolute;
          left: 0.2rem;
          top: 0.15rem;
        }
      }
    }
    .icon {
      width: 0.35rem;
      height: 0.35rem;
      margin-top: 0.05rem;
    }
    .topMain {
      font-size: 0.15rem;
      color: rgba(255, 255, 255, 1);
      line-height: 0.35rem;
    }
    .center {
      font-size: 0.12rem;
      color: rgba(132, 136, 140, 1);
    }
    .bottomMain {
      display: flex;
      margin-top: 0.15rem;
      padding: 0 0.58rem 0 0.58rem;
      span {
        font-size: 0.14rem;
        color: rgba(153, 153, 153, 1);
        width: 0.67rem;
        height: 0.2rem;
        background: rgba(57, 62, 66, 1);
        border-radius: 0.1rem;
      }
    }
    .num {
      font-size: 0.3rem;
      font-weight: 500;
      color: rgba(255, 255, 255, 1);
      margin-top: 0.1rem;
    }
    .content {
      display: flex;
      padding-left: 0.72rem;
      margin-top: 0.03rem;
      div {
        flex: 1;
        text-align: left;
      }
      .top {
        font-size: 0.15rem;
        color: rgba(255, 255, 255, 1);
      }
      .bottom {
        font-size: 0.15rem;
        font-weight: 500;
        line-height: 0.26rem;
        color: rgba(255, 255, 255, 1);
      }
    }
    .footer {
      position: absolute;
      left: 0;
      bottom: 0.1rem;
      width: 3.75rem;
      img {
        width: 0.13rem;
        height: 0.13rem;
        transform: translateY(0.01rem);
      }
      span {
        font-size: 0.14rem;
        color: rgba(205, 205, 207, 1);
      }
    }
  }
  .mainTip {
    font-size: 0.17rem;
    color: rgba(27, 27, 27, 1);
    margin-top: 0.24rem;
  }
  .erBox {
    width: 1.4rem;
    height: 1.4rem;
    border: 1px dashed rgba(204, 204, 204, 1);
    margin: auto;
    margin-top: 0.25rem;
    .mainEr {
      width: 1.25rem;
      height: 1.25rem;
      margin-top: 0.08rem;
    }
  }
  .erCode {
    margin-top: 0.27rem;
    img {
      width: 0.2rem;
      height: 0.2rem;
      transform: translateY(0.06rem);
      margin-left: 0.05rem;
    }
    span {
      font-size: 0.11rem;
      color: rgba(227, 60, 61, 1);
      line-height: 0.2rem;
      text-indent: 0.1rem;
      display: inline-block;
      padding-right: 0.1rem;
      background: rgba(247, 207, 208, 1);
      border-radius: 0.02rem;
    }
  }
  button {
    margin-top: 0.31rem;
    margin-bottom: 0.31rem;
    font-size: 0.17rem;
    color: rgba(255, 255, 255, 1);
    width: 3.45rem;
    height: 0.45rem;
    background: rgba(227, 60, 61, 1);
    border-radius: 0.03rem;
  }
}
</style>



