<template>
  <div class="fortuneTreeTeamManager">
    <div class="header">
      <div class="title">
        <i onClick="window.history.back()"></i>
        <p>财富自由之树团队管理</p>
      </div>
    </div>
    <div class="paragraph">
      <p>这里的一级会员指的是“等级”尚在一级的会员。</p>
      <p>例如:A升级到2级，那么A就会变动到您的二级会员之内。</p>
      <p>以此类推，所以请及时提醒等级较低的用户，完成升级任务。</p>
      <p>您的下级会员共计：xx人</p>
    </div>
    <div class="list">
      <ul class="list-ul">
        <li class="list-li">
          <i class="icon"></i>
          <span class="desc">一级会员（500人）</span>
          <img src="../../assets/btn_more_gray@2x.png" alt class="arrow" @click="handerPiano" />
        </li>
        <div class="piano" v-show="isShow">
          <div class="title">
            <span>注册时间</span>
            <span class="two">微信二维码</span>
            <i></i>
          </div>
          <ul class="piano-ul">
            <li class="piano-li">
              <span>2019-07-05</span>
              <div>
                <img src="../../assets/home_logo@2x.png" alt />
              </div>
              <p></p>
            </li>
            <li class="piano-li">
              <span>2019-07-05</span>
              <div>
                <img src="../../assets/home_logo@2x.png" alt />
              </div>
              <p></p>
            </li>
            <li class="piano-li">
              <span>2019-07-05</span>
              <div>
                <img src="../../assets/home_logo@2x.png" alt />
              </div>
              <p></p>
            </li>
          </ul>
        </div>
        <li class="list-li">
          <i class="icon"></i>
          <span class="desc">二级会员（500人）</span>
          <img src="../../assets/btn_more_gray@2x.png" alt class="arrow" @click="handerPiano" />
        </li>
        <li class="list-li">
          <i class="icon"></i>
          <span class="desc">三级会员（500人）</span>
          <img src="../../assets/btn_more_gray@2x.png" alt class="arrow" @click="handerPiano" />
        </li>
        <li class="list-li">
          <i class="icon"></i>
          <span class="desc">四级会员（500人）</span>
          <img src="../../assets/btn_more_gray@2x.png" alt class="arrow" @click="handerPiano" />
        </li>
        <li class="list-li">
          <i class="icon"></i>
          <span class="desc">五级会员（500人）</span>
          <img src="../../assets/btn_more_gray@2x.png" alt class="arrow" @click="handerPiano" />
        </li>
      </ul>
    </div>
    <div class="fixed">
      <ul>
        <li>
          <img src="../../../static/image_yuan@2x.png" alt />
          <p></p>
        </li>
        <li>
          <router-link to="fortuneTreeDetail">
            <img src="../../../static/rocket_icon_normal@2x.png" alt />
            <p>升级</p>
          </router-link>
        </li>
        <li>
          <router-link to="fortuneTreeTeamManager">
            <img src="../../../static/team_icon_click@2x.png" alt />
            <p>团队</p>
          </router-link>
        </li>
        <li>
          <router-link to="fortuneTreeInvitation">
            <img src="../../../static/addfriend_icon_normal@2x.png" alt />
            <p>邀请</p>
          </router-link>
        </li>
        <li>
          <router-link to="fortuneDetail">
            <img src="../../../static/calculator_icon_normal@2x.png" alt />
            <p>财务</p>
          </router-link>
        </li>
        <li>
          <router-link to="fortuneTreeDetailMessage">
            <img src="../../../static/news_icon_normal@2x.png" alt />
            <p>信息</p>
          </router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "FortuneTreeTeamManager",
  data() {
    return {
      isShow: false
    };
  },
  methods: {
    handerPiano() {
      this.isShow = true;
    }
  }
};
</script>

<style lang='less' scoped>
.fortuneTreeTeamManager {
  background-color: rgb(248, 248, 248);
  height: 100vh;
  .header {
    width: 3.75rem;
    height: 0.44rem;
    background: rgba(57, 58, 62, 1);
    .title {
      overflow: hidden;
      position: relative;
      p {
        font-weight: 500;
        color: rgba(255, 255, 255, 1);
        margin-top: 0.12rem;
      }
      i {
        background: url("../../../static/btn_back_white@2x.png");
        background-size: cover;
        width: 0.16rem;
        height: 0.16rem;
        position: absolute;
        left: 0.2rem;
        top: 0.15rem;
      }
    }
  }
  .paragraph {
    background-color: #fff;
    padding: 0.15rem;
    p {
      text-align: left;
      color: rgba(64, 64, 64, 1);
    }
    p:nth-child(4) {
      font-size: 0.14rem;
      margin-top: 0.15rem;
      color: rgba(19, 145, 242, 1);
    }
  }
  .list {
    margin-top: 0.1rem;
    .list-ul {
      .list-li {
        position: relative;
        height: 0.45rem;
        background-color: #fff;
        i {
          background: url("../../../static/dengji_icon@2x.png");
          width: 0.2rem;
          height: 0.16rem;
          background-size: cover;
          position: absolute;
          left: 0.2rem;
          top: 0.14rem;
        }
        span {
          font-size: 0.14rem;
          color: rgba(63, 63, 63, 1);
          position: absolute;
          left: 0.51rem;
          top: 0.14rem;
        }
        img {
          width: 0.12rem;
          transform: rotate(90deg);
          position: absolute;
          left: 2.98rem;
          top: 0.19rem;
        }
      }
      .piano {
        .title {
          height: 0.3rem;
          background-color: rgb(248, 248, 248);
          display: flex;
          align-items: center;
          span {
            flex: 3;
            font-size: 0.13rem;
            color: rgba(81, 82, 84, 1);
          }
          .two {
            flex: 3;
          }
          i {
            flex: 2;
          }
        }
        .piano-ul {
          .piano-li {
            display: flex;
            align-items: center;
            background-color: #fff;
            height: 0.45rem;
            span {
              flex: 3;
              font-size: 0.12rem;
              color: rgba(102, 102, 102, 1);
            }
            div {
              flex: 3;
              display: flex;
              align-items: center;
              justify-content: center;
              img {
                width: 0.25rem;
                height: 0.25rem;
                transform: rotate(0);
              }
            }
            p {
              flex: 2;
            }
          }
        }
      }
    }
  }
  .fixed {
    position: fixed;
    right: 0.21rem;
    top: 2.23rem;
    ul {
      li {
        margin-bottom: 0.2rem;
        img {
          width: 0.24rem;
          height: 0.24rem;
        }
        p {
          font-size: 0.12rem;
          color: rgba(34, 34, 34, 1);
        }
      }
      li:nth-child(1) {
        img {
          width: 0.35rem;
          height: 0.35rem;
        }
      }
    }
  }
}
</style>



