<template>
  <div class="headPreview">
    <Header title="头像预览"></Header>
    <i @click="tapIcon">...</i>
    <img src="../../../static/image_fang @2x.png" alt id="img"/>
    <div class="popup" v-show="isPopup">
      <div class="content">
        <p class="top">更改头像</p>
        <hr />
        <input type="file" name id="file" accept="image/*" />
        <p class="bottom" @click="tapSelect">从手机相册选择</p>
      </div>
      <button @click="tapEsc">取消</button>
    </div>
  </div>
</template>

<script>
import Header from "../customize/Header";
export default {
  name: "HeadPreview",
  components: {
    Header
  },
  data() {
    return {
      isPopup: false
    };
  },
  methods: {
    tapIcon() {
      this.isPopup = true;
    },
    tapEsc() {
      this.isPopup = false;
    },
    tapSelect() {
      $('#file').click()
    }
  },
  mounted() {
    const fileTag = document.getElementById("file");
    fileTag.onchange = () => {
      const file = fileTag.files[0];
      const fileReader = new FileReader();
      fileReader.onloadend = () => {
        if (fileReader.readyState == fileReader.DONE) {
          document.getElementById("img").setAttribute("src", fileReader.result);
          this.isPopup = false
        }
      };
      fileReader.readAsDataURL(file);
    };
  }
};
</script>

<style lang="less" scoped>
.headPreview {
  padding-top: 0.44rem;
  background-color: #24292e;
  height: 100vh;
  i {
    position: fixed;
    width: 0.15rem;
    line-height: 0.15rem;
    top: 0.1rem;
    right: 0.15rem;
    color: #fff;
    padding-bottom: 0.07rem;
  }
  img {
    width: 3.45rem;
    margin-top: 0.9rem;
  }
  .popup {
    position: fixed;
    left: 0;
    top: 0;
    width: 3.75rem;
    height: 100vh;
    background-color: rgba(129, 129, 129, 0.315);
    .content {
      width: 3.45rem;
      height: 0.91rem;
      background: rgba(255, 255, 255, 1);
      border-radius: 0.03rem;
      position: absolute;
      bottom: 0.75rem;
      left: 0.15rem;
      .top {
        font-size: 0.13rem;
        color: rgba(153, 153, 153, 1);
        line-height: 0.4rem;
      }
      hr {
        width: 3.45rem;
        border: 0.5px solid #e7e7e7;
      }
      #file {
        display: none;
      }
      .bottom {
        font-size: 0.18rem;
        color: rgba(218, 0, 0, 1);
        line-height: 0.5rem;
      }
    }
    button {
      width: 3.45rem;
      position: absolute;
      bottom: 0.15rem;
      left: 0.15rem;
      background: rgba(255, 255, 255, 1);
      border-radius: 0.03rem;
      font-size: 0.17rem;
      color: rgba(218, 0, 0, 1);
      line-height: 0.45rem;
    }
  }
}
</style>


