<template>
  <div class="freetreeTotalincome">
    <Header title="财富自由之树总收益" class="header"></Header>
    <div class="tabContain">
      <ul class="tab">
        <li class="active" id="tabOne" @click="tapTab">财富树总收入</li>
        <li @click="tapTab" id="tabTwo">财富树推广收入</li>
      </ul>
    </div>
    <component :is="currentTap"></component>
  </div>
</template>

<script>
import Header from "../customize/Header";
import FreetreeAlltab from "../mi/FreetreeAlltab";
import FreetreeExtendtab from "../mi/FreetreeExtendtab";
export default {
  name: "FreetreeTotalincome",
  data() {
    return {
      currentTap: "FreetreeAlltab"
    };
  },
  components: {
    Header,
    FreetreeAlltab,
    FreetreeExtendtab
  },
  methods: {
    tapTab(e) {
      const el = e.target.id;
      if (el === "tabOne") {
        $("#tabOne")
          .addClass("active")
          .siblings()
          .removeClass("active");
        this.currentTap = "FreetreeAlltab";
      } else if (el === "tabTwo") {
        $("#tabTwo")
          .addClass("active")
          .siblings()
          .removeClass("active");
        this.currentTap = "FreetreeExtendtab";
      }
    }
  }
};
</script>

<style lang='less' scoped>
.freetreeTotalincome {
  background-color: rgb(248, 248, 248);
  .header{
    z-index: 2;
  }
  .tabContain {
    margin-top: 0.44rem;
    .tab {
      display: flex;
      li {
        line-height: 0.49rem;
        flex: 1;
        position: relative;
        color:rgba(5,5,5,1);
        &::after {
          content: "";
          display: inline-block;
          position: absolute;
          left: 0.48rem;
          top: 0.47rem;
          width: 0rem;
          height: 0.02rem;
          background: rgba(227, 60, 61, 1);
          border-radius: 0.02rem;
        }
      }
      .active {
        color:rgba(227,60,61,1);
        &::after {
          width: 0.9rem;
        }
      }
    }
  }
}
</style>



