import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import Tabbar from '@/components/Tabbar'
import Hongbaoqing from '@/components/Hongbaoqing'
import Caifusu from '@/components/Caifusu'
import Mi from '@/components/Mi'
import Signin from '@/components/Signin'
import Signon from '@/components/Signon'
import HomeCurrency from '@/components/HomeCurrency'
import HomeCurrencySearchResult from '@/components/HomeCurrencySearchResult'
import FortuneTreeRedPackSearch from '@/components/FortuneTreeRedPackSearch'
import RedEnvelopeGroup from '@/components/RedEnvelopeGroup'
import GroupMembers from '@/components/GroupMembers'
import RedEnvelopeDetails from '@/components/index/RedEnvelopeDetails'
import RedMatchingDragon from '@/components/index/RedMatchingDragon'
import CurrencyRoom from '@/components/index/CurrencyRoom'
import FortuneTreeJoin from '@/components/index/FortuneTreeJoin'
import RedpackGroup from '@/components/create/RedpackGroup'
import FortuneTree from '@/components/create/FortuneTree'
import FortuneTreeDetail from '@/components/fortuneTree/FortuneTreeDetail'
import FortuneTreeTeamManager from '@/components/fortuneTree/FortuneTreeTeamManager'
import FortuneTreeInvitation from '@/components/fortuneTree/FortuneTreeInvitation'
import FortuneDetail from '@/components/fortuneTree/FortuneDetail'
import SignonSuccess from '@/components/mi/SignonSuccess'
import SignonSuccessNext from '@/components/mi/SignonSuccessNext'
import PersonalFunds from '@/components/mi/PersonalFunds'
import FillingupRecords from '@/components/mi/FillingupRecords'
import RedTotalincome from '@/components/mi/RedTotalincome'
import FreetreeTotalincome from '@/components/mi/FreetreeTotalincome'
import WechatBind from '@/components/mi/WechatBind'
import InviteFriend from '@/components/mi/InviteFriend'
import Feedback from '@/components/mi/Feedback'
import RuleNotice from '@/components/mi/RuleNotice'
import PersonalInformation from '@/components/mi/PersonalInformation'
import SetNickname from '@/components/mi/SetNickname'
import FortuneTreeDetailMessage from '@/components/fortuneTree/FortuneTreeDetailMessage'
import InviteFriendGrabRedpack from '@/components/hongbaoqun/InviteFriendGrabRedpack'
import HeadPreview from '@/components/mi/HeadPreview'
import CurrencyTypes from '@/components/mi/CurrencyTypes'
import WithdRawal from '@/components/mi/WithdRawal'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Tabbar',
      component: Tabbar,
      children: [
        {
          path: '',
          component: Index
        },
        {
          path: 'index',
          component: Index
        },
        {
          path: 'hongbaoqing',
          component: Hongbaoqing
        },
        {
          path: 'caifusu',
          component: Caifusu
        },
        {
          path: 'mi',
          component: Mi
        },
      ]
    },
    {
      path: '/signin',
      name: 'Signin',
      component: Signin,
    },
    {
      path: '/signon',
      name: 'Signon',
      component: Signon,
    },
    {
      path: '/homeCurrency',
      name: 'HomeCurrency',
      component: HomeCurrency,
    },
    {
      path: '/homeCurrencySearchResult',
      name: 'HomeCurrencySearchResult',
      component: HomeCurrencySearchResult,
    },
    {
      path: '/fortuneTreeRedPackSearch',
      name: 'FortuneTreeRedPackSearch',
      component: FortuneTreeRedPackSearch,
    },
    {
      path: '/redEnvelopeGroup',
      name: 'RedEnvelopeGroup',
      component: RedEnvelopeGroup,
    },
    {
      path: '/groupMembers',
      name: 'GroupMembers',
      component: GroupMembers,
    },
    {
      path: '/redEnvelopeDetails',
      name: 'RedEnvelopeDetails',
      component: RedEnvelopeDetails,
    },
    {
      path: '/redMatchingDragon',
      name: 'RedMatchingDragon',
      component: RedMatchingDragon,
    },
    {
      path: '/currencyRoom',
      name: 'CurrencyRoom',
      component: CurrencyRoom,
    },
    {
      path: '/redpackGroup',
      name: 'RedpackGroup',
      component: RedpackGroup,
    },
    {
      path: '/fortuneTree',
      name: 'FortuneTree',
      component: FortuneTree,
    },
    {
      path: '/fortuneTreeJoin',
      name: 'FortuneTreeJoin',
      component: FortuneTreeJoin,
    },
    {
      path: '/fortuneTreeDetail',
      name: 'FortuneTreeDetail',
      component: FortuneTreeDetail,
    },
    {
      path: '/fortuneTreeTeamManager',
      name: 'FortuneTreeTeamManager',
      component: FortuneTreeTeamManager,
    },
    {
      path: '/fortuneTreeInvitation',
      name: 'FortuneTreeInvitation',
      component: FortuneTreeInvitation,
    },
    {
      path: '/fortuneDetail',
      name: 'FortuneDetail',
      component: FortuneDetail,
    },
    {
      path: '/signonSuccess',
      name: 'SignonSuccess',
      component: SignonSuccess,
    },
    {
      path: '/signonSuccessNext',
      name: 'SignonSuccessNext',
      component: SignonSuccessNext,
    },
    {
      path: '/personalFunds',
      name: 'PersonalFunds',
      component: PersonalFunds,
    },
    {
      path: '/currencyTypes',
      name: 'CurrencyTypes',
      component: CurrencyTypes
    },
    {
      path: '/fillingupRecords',
      name: 'FillingupRecords',
      component: FillingupRecords,
    },
    {
      path: '/redTotalincome',
      name: 'RedTotalincome',
      component: RedTotalincome,
    },
    {
      path: '/freetreeTotalincome',
      name: 'FreetreeTotalincome',
      component: FreetreeTotalincome,
    },
    {
      path: '/wechatBind',
      name: 'WechatBind',
      component: WechatBind,
    },
    {
      path: '/inviteFriend',
      name: 'InviteFriend',
      component: InviteFriend,
    },
    {
      path: '/feedback',
      name: 'Feedback',
      component: Feedback,
    },
    {
      path: '/ruleNotice',
      name: 'RuleNotice',
      component: RuleNotice,
    },
    {
      path: '/personalInformation',
      name: 'PersonalInformation',
      component: PersonalInformation,
    },
    {
      path: '/setNickname',
      name: 'SetNickname',
      component: SetNickname,
    },
    {
      path: '/fortuneTreeDetailMessage',
      name: 'FortuneTreeDetailMessage',
      component: FortuneTreeDetailMessage,
    },
    {
      path: '/inviteFriendGrabRedpack',
      name: 'InviteFriendGrabRedpack',
      component: InviteFriendGrabRedpack,
    },
    {
      path: '/headPreview',
      name: 'HeadPreview',
      component: HeadPreview,
    },
    {
      path: '/withdRawal',
      name: 'WithdRawal',
      component: WithdRawal
    }
  ]
})



