<template>
  <div class="fortuneTreeRedPackSearch">
    <div class="search">
      <img src="../assets/btn_search_gray@2x.png" alt @click="handerSearch" />
      <input type="text" placeholder="请输入币种、财富树密码、红包房间号" />
    </div>
    <router-link to="/">
      <span class="esc">取消</span>
    </router-link>
    <ul class="list" v-show="isList">
      <li>
        <img src="../assets/icon_bizhong@2x.png" alt />
        <p>Xx个币xx个包</p>
        <p>在线人员数25</p>
        <span>最大/最小的发</span>
        <router-link to="/redEnvelopeGroup">
          <button>前往</button>
        </router-link>
      </li>
      <div class="hr"></div>
    </ul>
  </div>
</template>

<script>
export default {
  name: "FortuneTreeRedPackSearch",
  data() {
    return {
      isList: false
    };
  },
  methods: {
    handerSearch() {
      this.isList = true;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='less' scoped>
.fortuneTreeRedPackSearch {
  padding-top: 0.2rem;
  height: 100vh;
  box-sizing: border-box;
  background-color: rgb(245, 245, 245);
  position: relative;
  .search {
    position: relative;
    margin-left: 0.15rem;
    width: 3.13rem;
    height: 0.32rem;
    background: rgba(255, 255, 255, 1);
    border-radius: 0.04rem;
    img {
      width: 0.13rem;
      position: absolute;
      left: 0.1rem;
      bottom: 0.1rem;
    }
    input {
      width: 2.6rem;
      padding-left: 0.1rem;
      position: absolute;
      left: 0.3rem;
      top: 0.09rem;
      border: none;
    }
  }
  .esc {
    height: 0.12rem;
    font-size: 0.12rem;
    font-weight: 500;
    color: rgba(78, 152, 239, 1);
    line-height: 0.15rem;
    position: absolute;
    top: 0.3rem;
    right: 0.13rem;
  }
  .list {
    width: 3.75rem;
    background: rgba(255, 255, 255, 1);
    margin-top: 0.1rem;
    .hr {
      width: 3.45rem;
      height: 0.01rem;
      background: rgba(247, 247, 247, 1);
      margin-left: 0.15rem;
    }
    li {
      width: 3.75rem;
      height: 0.73rem;
      background: rgba(255, 255, 255, 1);
      position: relative;
      img {
        width: 0.4rem;
        position: absolute;
        left: 0.15rem;
        bottom: 0.17rem;
      }
      p {
        height: 0.13rem;
        font-size: 0.14rem;
        color: rgba(64, 64, 64, 1);
        line-height: 0.08rem;
        position: absolute;
        left: 0.65rem;
        top: 0.21rem;
      }
      p:nth-child(3) {
        height: 0.12rem;
        font-size: 0.12rem;
        color: rgba(64, 64, 64, 1);
        line-height: 0.08rem;
        position: absolute;
        left: 2.87rem;
        top: 0.21rem;
      }
      span {
        height: 0.1rem;
        font-size: 0.1rem;
        color: rgba(153, 153, 153, 1);
        line-height: 0.08rem;
        position: absolute;
        left: 0.66rem;
        top: 0.41rem;
      }
      button {
        width: 0.32rem;
        height: 0.17rem;
        background: rgba(250, 189, 105, 1);
        font-size: 0.12rem;
        color: rgba(255, 255, 255, 1);
        line-height: 0.17rem;
        border-radius: 0.02rem;
        position: absolute;
        right: 0.15rem;
        top: 0.38rem;
      }
    }
  }
}
</style>



