
import Vue from 'vue'
import Vuex from 'vuex'
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'
import $ from 'n-zepto'
import axios from 'axios'
import App from './App'
import router from './router'

Vue.use(MintUI)
Vue.use(Vuex)
axios.defaults.baseURL = 'http://192.168.24.159:20081/api/'
// axios.defaults.headers = {token: this.$store.state.token, sessionid: this.$store.state.sessionid}
Vue.prototype.$axios = axios

const store = new Vuex.Store({
  state: {
    isLogin: false,
    username: '',
    password: '',
    token: '',
    sessionid: '',
  },
  mutations: {
    // checkLogin (state) {
    //   state.isLogin = true
    // },
    handerQuit (state) {
      state.isLogin = false
      state.token = ''
      state.sessionid = ''
    },
  }
})

Vue.config.productionTip = false

new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



