<template>
  <div class="redTotalincome">
    <div class="banner">
      <div class="header">
        <div class="title">
          <i onClick='window.history.back()'></i>
          <p>红包总收益</p>
        </div>
      </div>
      <img src="../../../static/usd_logo@2x.png" alt class="icon" />
      <p class="topMain">USDT</p>
      <p class="center"></p>
      <div class="bottomMain">
        <span>累计收益</span>
        <i></i>
      </div>
      <p class="num">0</p>
      <div class="footer">
        <ul class="tab">
          <li class="active" id="tabOne" @click="tapTab">推广收益</li>
          <li @click="tapTab" id="tabTwo">群主收益</li>
          <li id="tabThree" @click="tapTab">抢红包流水</li>
          <li id="tabFour" @click="tapTab">我的下级</li>
        </ul>
      </div>
    </div>
    <component :is="currentTap"></component>
  </div>
</template>

<script>
import ExtensionIncome from "./ExtensionIncome";
import GroupIncome from "./GroupIncome";
import WaterInRed from "./WaterInRed";
import Subordinate from "./Subordinate";
export default {
  name: "RedTotalincome",
  data() {
    return {
      currentTap: "ExtensionIncome"
    };
  },
  components: {
    ExtensionIncome,
    GroupIncome,
    WaterInRed,
    Subordinate
  },
  methods: {
    tapTab(e) {
      const el = e.target.id;
      if (el === "tabOne") {
        $("#tabOne")
          .addClass("active")
          .siblings()
          .removeClass("active");
        this.currentTap = "ExtensionIncome";
      } else if (el === "tabTwo") {
        $("#tabTwo")
          .addClass("active")
          .siblings()
          .removeClass("active");
        this.currentTap = "GroupIncome";
      } else if (el === "tabThree") {
        $("#tabThree")
          .addClass("active")
          .siblings()
          .removeClass("active");
        this.currentTap = "WaterInRed";
      } else if (el === "tabFour") {
        $("#tabFour")
          .addClass("active")
          .siblings()
          .removeClass("active");
        this.currentTap = "Subordinate";
      }
    }
  }
};
</script>

<style lang='less' scoped>
.redTotalincome {
  background-color: rgb(248, 248, 248);
  min-height: 100vh;
  padding-bottom: 0.2rem;
  box-sizing: border-box;
  .banner {
    height: 3.25rem;
    background: url("../../../static/black_bg@2x.png");
    background-size: cover;
    .header {
      width: 3.75rem;
      height: 0.44rem;
      .title {
        overflow: hidden;
        position: relative;
        height: 0.44rem;
        p {
          font-weight: 500;
          color: rgba(255, 255, 255, 1);
          line-height: 0.15rem;
          margin-top: 0.14rem;
        }
        i {
          background: url("../../../static/btn_back_white@2x.png");
          background-size: cover;
          width: 0.16rem;
          height: 0.16rem;
          position: absolute;
          left: 0.2rem;
          top: 0.14rem;
        }
      }
    }
    .icon {
      width: 0.35rem;
      height: 0.35rem;
      margin-top: 0.05rem;
    }
    .topMain {
      font-size: 0.15rem;
      color: rgba(255, 255, 255, 1);
      line-height: 0.35rem;
    }
    .center {
      font-size: 0.12rem;
      color: rgba(132, 136, 140, 1);
      background: url("../../../static/qiehuan_red_icon@2x.png");
      width: 0.17rem;
      height: 0.12rem;
      background-size: cover;
      margin: auto;
    }
    .bottomMain {
      margin-top: 0.15rem;
      span {
        font-size: 0.13rem;
        color: rgba(205, 205, 207, 1);
        margin-right: 0.07rem;
      }
      i {
        background: url("../../../static/i_icon@2x.png");
        display: inline-block;
        width: 0.12rem;
        height: 0.12rem;
        background-size: cover;
        transform: translateY(0.02rem);
      }
    }
    .num {
      font-size: 0.3rem;
      font-weight: 500;
      color: rgba(255, 255, 255, 1);
      margin-top: 0.1rem;
    }
    .footer {
      .tab {
        margin-top: 0.4rem;
        height: 0.49rem;
        display: flex;
        li {
          line-height: 0.49rem;
          flex: 1;
          position: relative;
          font-size: 0.13rem;
          color: rgba(205, 205, 207, 1);
          &::after {
            content: "";
            display: inline-block;
            position: absolute;
            left: 0.43rem;
            top: 0.4rem;
            width: 0.08rem;
            height: 0.04rem;
            background: rgba(58, 207, 255, 1);
            border-radius: 0.02rem;
          }
        }
        li:nth-child(2) {
          &::after {
            background: rgba(249, 72, 36, 1);
          }
        }
        li:nth-child(3) {
          &::after {
            background: rgba(223, 186, 142, 1);
          }
        }
        li:nth-child(4) {
          &::after {
            background: rgba(223, 142, 142, 1);
          }
        }
        .active {
          &::after {
            width: 0.4rem;
            left: 0.28rem;
          }
        }
      }
    }
  }
}
</style>



