<template>
  <div class="wechatBind">
    <Header title="微信绑定"></Header>
    <div class="contain">
      <div class="title">上传微信二维码</div>
      <hr />
      <div class="subContain">
        <img src="../../../static/shangchuan_image@2x.png" alt @click="upImg" id="img" />
        <input type="file" accept="image/*" id="upBtn" />
      </div>
    </div>
  </div>
</template>

<script>
import Header from "../customize/Header";
export default {
  name: "WechatBind",
  data() {
    return {};
  },
  components: {
    Header
  },
  methods: {
    upImg() {
      $("#upBtn").click();
    }
  },
  mounted() {
    const fileTag = document.getElementById("upBtn");
    fileTag.onchange = () => {
      const file = fileTag.files[0];
      const fileReader = new FileReader();
      fileReader.onloadend = () => {
        if (fileReader.readyState == fileReader.DONE) {
          document.getElementById("img").setAttribute("src", fileReader.result);
        }
      };
      fileReader.readAsDataURL(file);
    };
  }
};
</script>

<style lang='less' scoped>
.wechatBind {
  background-color: rgb(248, 248, 248);
  height: 100vh;
  overflow: hidden;
  .contain {
    margin-top: 0.44rem;
    background-color: #fff;
    .title {
      height: 0.55rem;
      color: rgba(34, 34, 34, 1);
      line-height: 0.55rem;
      text-align: left;
      padding-left: 0.15rem;
    }
    hr {
      border: 0.5px solid #e7e7e7;
      margin: 0;
    }
    .subContain {
      padding: 0.35rem 0 0.35rem 0;
      img {
        width: 1.4rem;
      }
      input {
        display: none;
      }
    }
  }
}
</style>



