<template>
  <div class="signin">
    <img src="../assets/app_cancel_icon@2x.png" class="closs" @click="()=>this.$router.push('mi')" />
    <img src="../assets/home_logo@2x.png" alt class="logo" />
    <p class="title">EOSATM</p>
    <p class="youqing">邀请码</p>
    <ul class="list">
      <li>
        <img src="../assets/login_input_icon@2x.png" alt />
        <input type="text" placeholder="请输入邀请码" v-model="invCode" />
      </li>
    </ul>
    <div class="reg_box"><button @click="tapRegister">立即注册</button></div>
  </div>
</template>

<script>
export default {
  name: "Signin",
  data() {
    return {
      invCode: ""
    };
  },
  methods: {
    tapRegister() {
      this.$axios
        .post("user/register", {
          inviteCode: this.invCode
        })
        .then(res => {
          const data = res.data.data
          if (res.data.status === 1) {
            this.$store.state.username = data.username
            this.$store.state.password = data.password
            this.$router.push({ name: 'SignonSuccess', params: { title: "注册成功", isShowBtn: true }});
          } else {
            this.$toast("请输入正确的邀请码");
          }
        })
        .catch(error => {
          this.$toast("访问地址有误");
        });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='less' scoped>
.signin {
  height: 100vh;
  background-color: #F5F5F5;
  position: relative;
  .closs {
    width: 0.16rem;
    position: absolute;
    top: 0.15rem;
    right: 0.15rem;
  }
  .logo {
    width: 0.87rem;
    padding-top: .5rem;
    display: block;
    margin: auto;
  }
  .title {
    font-size: 0.31rem;
    font-family: Noto Sans S Chinese;
    font-weight: bold;
    color: rgba(0, 9, 18, 1);
    margin: auto;
    padding-top: .16rem;
  }
  .youqing {
    font-size: 0.18rem;
    font-weight: 500;
    color: rgba(227, 60, 61, 1);
    margin: auto;
    padding-top: .54rem;
  }
  .list {
    padding: .2rem .15rem .25rem;
    li {
      width: 100%;
      height: 0.45rem;
      background: rgba(255, 255, 255, 1);
      border-radius: 0.03rem;
      position: relative;
      img {
        width: 0.16rem;
        position: absolute;
        left: .14rem;
        top: .14rem;
      }
      input {
        height: .43rem;
        font-size: 0.15rem;
        color: rgba(181, 184, 199, 1);
        border: none;
        position: absolute;
        left: .45rem;
        top: 0;
        width: 80%;
      }
    }
  }
  .reg_box{
    padding: 0 .15rem;
    button {
      width: 100%;
      height: .45rem;
      background: rgba(227, 60, 61, 1);
      border-radius: 0.03rem;
      letter-spacing: .01rem;
    }
  }
}
</style>



