<template>
  <div class="fortuneTreeDetailMessage">
    <div class="header">
      <div class="title">
        <i onClick="window.history.back()"></i>
        <p>财富树信息</p>
      </div>
    </div>
    <div class="list">
      <ul>
        <li>
          <i></i>
          <span>消费使用币种</span>
          <p>USDT</p>
        </li>
        <li>
          <i></i>
          <span>层级</span>
          <p>8</p>
        </li>
        <li>
          <i></i>
          <span>金额</span>
          <p>升一级x币、升二级x个币、升三级x个币</p>
        </li>
        <li>
          <i></i>
          <span>推广制度</span>
          <p>一推x，太阳线推广（无推广限制）</p>
        </li>
        <li>
          <i></i>
          <span>当前成长级</span>
          <p>xx级</p>
        </li>
        <li>
          <i></i>
          <span>当前参与人数</span>
          <p>xx人数</p>
        </li>
      </ul>
    </div>
    <button>立即开通</button>
    <div class="fixed">
      <ul>
        <li>
          <img src="../../../static/image_yuan@2x.png" alt />
          <p></p>
        </li>
        <li>
          <router-link to="fortuneTreeDetail">
            <img src="../../../static/rocket_icon_normal@2x.png" alt />
            <p>升级</p>
          </router-link>
        </li>
        <li>
          <router-link to="fortuneTreeTeamManager">
            <img src="../../../static/team_icon_normal@2x.png" alt />
            <p>团队</p>
          </router-link>
        </li>
        <li>
          <router-link to="fortuneTreeInvitation">
            <img src="../../../static/addfriend_icon_normal@2x.png" alt />
            <p>邀请</p>
          </router-link>
        </li>
        <li>
          <router-link to="fortuneDetail">
            <img src="../../../static/calculator_icon_normal@2x.png" alt />
            <p>财务</p>
          </router-link>
        </li>
        <li>
          <router-link to="fortuneTreeDetailMessage">
            <img src="../../../static/news_icon_click@2x.png" alt />
            <p>信息</p>
          </router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "FortuneTreeDetailMessage"
};
</script>

<style lang='less' scoped>
.fortuneTreeDetailMessage {
  height: 110vh;
  .header {
    width: 3.75rem;
    height: 2.2rem;
    background: url("../../../static/xinxi_background@2x.png");
    background-size: cover;
    .title {
      overflow: hidden;
      position: relative;
      p {
        font-weight: 500;
        color: rgba(51, 51, 51, 1);
        margin-top: 0.14rem;
      }
      i {
        background: url("../../../static/leftarrow_black_icon@2x.png");
        background-size: cover;
        width: 0.16rem;
        height: 0.16rem;
        position: absolute;
        left: 0.17rem;
        top: 0.16rem;
      }
    }
  }
  .list {
    ul {
      overflow: hidden;
      li {
        position: relative;
        height: 0.5rem;
        margin-top: 0.2rem;
        i {
          display: inline-block;
          width: 0.27rem;
          height: 0.27rem;
          background: url("../../../static/bizhong_icon@2x.png");
          background-size: cover;
          position: absolute;
          left: 0.87rem;
          top: 0;
        }
        span {
          font-size: 0.15rem;
          font-weight: bold;
          color: rgba(247, 181, 0, 1);
          position: absolute;
          left: 1.29rem;
          top: 0.04rem;
        }
        p {
          font-size: 0.1rem;
          color: rgba(119, 118, 118, 1);
          width: 1.14rem;
          position: absolute;
          left: 1.29rem;
          top: 0.28rem;
          text-align: left;
        }
      }
      li:nth-child(2) {
        i {
          background: url("../../../static/cengji_icon@2x.png");
          background-size: cover;
        }
      }
      li:nth-child(3) {
        i {
          background: url("../../../static/jiner_icon@2x.png");
          background-size: cover;
        }
      }
      li:nth-child(4) {
        i {
          background: url("../../../static/zhidu_icon@2x.png");
          background-size: cover;
        }
      }
      li:nth-child(5) {
        i {
          background: url("../../../static/dengji_icon@2x.png");
          background-size: cover;
        }
      }
      li:nth-child(6) {
        i {
          background: url("../../../static/renshu_icon@2x.png");
          background-size: cover;
        }
      }
    }
  }
  button {
    width: 3.45rem;
    height: 0.45rem;
    background: rgba(227, 60, 61, 1);
    border-radius: 0.03rem;
    font-size: 0.17rem;
    color: rgba(245, 245, 245, 1);
    margin-top: 0.2rem;
  }
  .fixed {
    position: fixed;
    right: 0.21rem;
    top: 2.23rem;
    ul {
      li {
        margin-bottom: 0.2rem;
        img {
          width: 0.24rem;
          height: 0.24rem;
        }
        p {
          font-size: 0.12rem;
          color: rgba(34, 34, 34, 1);
        }
      }
      li:nth-child(1) {
        img {
          width: 0.35rem;
          height: 0.35rem;
        }
      }
    }
  }
}
</style>



