<template>
  <div class="header">
    <div class="title">
      <i onClick='window.history.back()'></i>
      <p>{{title}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "Header",
  props:['title'],
  data() {
    return {};
  },
  methods: {}
};
</script>

<style lang='less' scoped>
.header {
  background:rgba(57,58,62,1);
  width: 3.75rem;
  height: 0.44rem;
  position: fixed;
  left: 0;top: 0;
  .title {
    overflow: hidden;
    position: relative;
    height: 0.44rem;
    p {
      font-weight: 500;
      color: rgba(255, 255, 255, 1);
      line-height: 0.15rem;
      margin-top: 0.16rem;
    }
    i {
      background: url("../../../static/btn_back_white@2x.png");
      background-size: cover;
      width: 0.16rem;
      height: 0.16rem;
      position: absolute;
      left: 0.2rem;
      top: 0.16rem;
    }
  }
}
</style>



