<template>
  <div class="signonSuccess">
    <div class="header">
      <div class="title">
        <i onClick="window.history.back()"></i>
        <p>{{title}}</p>
      </div>
    </div>
    <div class="popup">
      <p class="tip">请截图或者使用文本，妥善备份您的账号密码，账户一旦丢失，账户内的资产永久不可找回。</p>
      <div class="main">
        <p class="top">您的账号</p>
        <p class="bottom" id="counterText">{{username}}</p>
        <button @click="copyCounter">复制账号</button>
      </div>
      <hr style="border: 0.5px dashed rgba(160,160,160,1); width:2.3rem;" />
      <div class="main">
        <p class="top">您的密码</p>
        <p class="bottom" id="psdText">{{password}}</p>
        <button @click="copyPsd">复制密码</button>
      </div>
    </div>
    <button
      class="verify"
      @click="()=>this.$router.push('signonSuccessNext')"
      v-show="isShowBtn"
    >我已经备份好，立刻验证</button>
  </div>
</template>

<script>
import { copyFun } from "../../assets/js/unit";
export default {
  name: "SignonSuccess",
  data() {
    return {
      isShowBtn: true,
      title: "注册成功",
      username: "",
      password: ""
    };
  },
  methods: {
    copyCounter() {
      copyFun("counterText");
    },
    copyPsd() {
      copyFun("psdText");
    }
  },
  beforeMount() {
    this.title = this.$route.params.title;
    this.isShowBtn = this.$route.params.isShowBtn;
    this.username = this.$store.state.username;
    this.password = this.$store.state.password;
  }
};
</script>

<style lang='less' scoped>
.signonSuccess {
  background-color: rgb(248, 248, 248);
  height: 100vh;
  .header {
    width: 3.75rem;
    height: 0.44rem;
    background: rgba(57, 58, 62, 1);
    .title {
      overflow: hidden;
      position: relative;
      p {
        font-weight: 500;
        color: rgba(255, 255, 255, 1);
        margin-top: 0.12rem;
      }
      i {
        background: url("../../../static/btn_back_white@2x.png");
        background-size: cover;
        width: 0.16rem;
        height: 0.16rem;
        position: absolute;
        left: 0.2rem;
        top: 0.14rem;
      }
    }
  }
  .popup {
    width: 3.45rem;
    background: rgba(255, 255, 255, 1);
    border-radius: 0.03rem;
    margin-top: 0.15rem;
    margin-left: 0.15rem;
    overflow: hidden;
    .tip {
      width: 3.2rem;
      margin-top: 0.28rem;
      margin-left: 0.15rem;
      font-size: 0.15rem;
      color: rgba(64, 64, 64, 1);
      text-align: left;
    }
    .main {
      margin-top: 0.2rem;
      margin-bottom: 0.2rem;
      .top {
        font-size: 0.15rem;
        color: rgba(64, 64, 64, 1);
      }
      .bottom {
        font-size: 0.14rem;
        color: rgba(227, 60, 61, 1);
        margin-top: 0.1rem;
        margin-bottom: 0.1rem;
      }
      button {
        font-size: 0.13rem;
        color: rgba(255, 255, 255, 1);
        width: 0.63rem;
        line-height: 0.2rem;
        background: rgba(227, 60, 61, 1);
        border-radius: 0.03rem;
      }
    }
  }
  .verify {
    font-size: 0.17rem;
    color: rgba(245, 245, 245, 1);
    line-height: 0.45rem;
    width: 3.45rem;
    margin-top: 0.2rem;
    background: rgba(227, 60, 61, 1);
    border-radius: 0.03rem;
  }
}
</style>



