<template>
  <div class="feedback">
    <div class="header">
      <div class="title">
        <i onClick='window.history.back()'></i>
        <p>意见反馈</p>
      </div>
    </div>
    <form action>
      <label for>问题和意见</label>
      <textarea name="comments" placeholder="具体说说您的问题，描述不少于20个字，我们会尽快为您解决。"></textarea>
      <label for>您的联系方式</label>
      <input type="email" name="contactWay" placeholder="邮箱" />
    </form>
    <button class="verify">提交</button>
  </div>
</template>

<script>
export default {
  name: "Feedback",
  data() {
    return {};
  },
  methods: {},
  beforeMount() {}
};
</script>

<style lang='less' scoped>
.feedback {
  background-color: rgb(248, 248, 248);
  height: 100vh;
  .header {
    width: 3.75rem;
    height: 0.44rem;
    background: rgba(57, 58, 62, 1);
    .title {
      overflow: hidden;
      position: relative;
      p {
        font-weight: 500;
        color: rgba(255, 255, 255, 1);
        margin-top: 0.11rem;
      }
      i {
        background: url("../../../static/btn_back_white@2x.png");
        background-size: cover;
        width: 0.16rem;
        height: 0.16rem;
        position: absolute;
        left: 0.2rem;
        top: 0.15rem;
      }
    }
  }
  form {
    overflow: hidden;
    label {
      border-left: 0.02rem solid rgba(227, 60, 61, 1);
      line-height: 0.16rem;
      font-size: 0.15rem;
      color: rgba(51, 51, 51, 1);
      display: block;
      text-align: left;
      margin: 0.1rem 0 0.1rem 0.15rem;
      padding-left: 0.1rem;
    }
    textarea {
      height: 1.5rem;
      background: rgba(240, 240, 240, 1);
      border-radius: 0.03rem;
      width: 3.45rem;
      padding: 0.1rem;
      font-family:PingFang SC;
    }
    input[type="email"] {
      height: 0.32rem;
      padding-left: 0.1rem;
      background: rgba(240, 240, 240, 1);
      border-radius: 0.03rem;
      width: 3.45rem;
    }
  }
  .verify {
    font-size: 0.17rem;
    color: rgba(245, 245, 245, 1);
    line-height: 0.45rem;
    width: 3.45rem;
    margin-top: 0.2rem;
    background: rgba(227, 60, 61, 1);
    border-radius: 0.03rem;
  }
}
</style>



