<template>
  <div class="homeCurrencySearchResult">
    <div class="search">
      <img src="../assets/btn_search_gray@2x.png" alt />
      <input type="text" placeholder="搜索" />
      <img src="../assets/btn_clear_gray@2x.png" alt id="clear" />
    </div>
    <router-link to="/">
      <span class="esc">取消</span>
    </router-link>
    <ul class="list">
      <li>
        <img src="../assets/icon_bizhong@2x.png" alt />
        <p>BTC</p>
        <span>bitcion</span>
      </li>
      <div class="hr"></div>
    </ul>
  </div>
</template>

<script>
export default {
  name: "HomeCurrencySearchResult",
  data() {
    return {
      msg: "1111111111111111111"
    };
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='less' scoped>
.homeCurrencySearchResult {
  padding-top: 0.2rem;
  height: 100vh;
  box-sizing: border-box;
  background-color: rgb(245, 245, 245);
  position: relative;
  .search {
    position: relative;
    margin-left: 0.15rem;
    width: 3.13rem;
    height: 0.32rem;
    background: rgba(255, 255, 255, 1);
    border-radius: 0.04rem;
    img {
      width: 0.13rem;
      position: absolute;
      left: 0.1rem;
      bottom: 0.1rem;
    }
    #clear {
      width: 0.16rem;
      position: absolute;
      left: 2.87rem;
      bottom: 0.08rem;
    }
    input {
      width: 2.2rem;
      padding-left: 0.1rem;
      position: absolute;
      left: 0.3rem;
      top: 0.09rem;
      border: none;
    }
  }
  .esc {
    height: 0.12rem;
    font-size: 0.12rem;
    font-weight: 500;
    color: rgba(78, 152, 239, 1);
    line-height: 0.15rem;
    position: absolute;
    top: 0.3rem;
    right: 0.13rem;
  }
  .list {
    width: 3.75rem;
    background: rgba(255, 255, 255, 1);
    margin-top: 0.1rem;
    .hr {
      width: 3.45rem;
      height: 0.01rem;
      background: rgba(247, 247, 247, 1);
      margin-left: 0.15rem;
    }
    li {
      width: 3.75rem;
      height: 0.65rem;
      background: rgba(255, 255, 255, 1);
      position: relative;
      img {
        width: 0.35rem;
        position: absolute;
        left: 0.15rem;
        bottom: 0.15rem;
      }
      p {
        height: 0.11rem;
        font-size: 0.14rem;
        color: rgba(64, 64, 64, 1);
        line-height: 0.08rem;
        position: absolute;
        left: 0.6rem;
        top: 0.21rem;
      }
      span {
        height: 0.09rem;
        font-size: 0.11rem;
        color: rgba(153, 153, 153, 1);
        line-height: 0.08rem;
        position: absolute;
        left: 0.6rem;
        top: 0.36rem;
      }
    }
  }
}
</style>



