<template>
  <div class="personalInformation">
    <Header title="个人信息"></Header>
    <div class="list">
      <ul>
        <li @click="()=>this.$router.push('headPreview')">
          <p>头像</p>
          <img src="../../../static/image_fang @2x.png" alt />
          <i></i>
          <hr />
        </li>
        <li @click="()=>this.$router.push('setNickname')">
          <p>昵称</p>
          <span>avdcc123</span>
          <i></i>
          <hr />
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import Header from "../customize/Header";
export default {
  name: "personalInformation",
  components: {
    Header
  },
  data() {
    return {
    };
  },
  methods: {
  }
};
</script>

<style lang="less" scoped>
.personalInformation {
  margin-top: 0.44rem;
  .list {
    ul {
      li {
        padding: 0.1rem 0.15rem;
        display: flex;
        position: relative;
        align-items: center;
        font-size: 0.14rem;
        color: rgba(63, 63, 63, 1);
        p {
          flex: 1;
          text-align: left;
        }
        img {
          width: 0.55rem;
          margin-right: 0.1rem;
          border-radius: 50%;
        }
        span {
          flex: 1;
          text-align: right;
          margin-right: 0.1rem;
        }
        i {
          background: url("../../../static/btn_more_gray@2x.png");
          background-size: cover;
          width: 0.12rem;
          height: 0.12rem;
        }
        hr {
          position: absolute;
          left: 0.15rem;
          top: 0.74rem;
          border: 0.5px solid #e7e7e7;
          width: 3.45rem;
          height: 0;
        }
      }
      li:nth-child(2) {
        hr {
          top: 0.46rem;
        }
      }
    }
  }
}
</style>


