<template>
  <div class="currencyTypes">
    <div class="header">
      <div class="title">
        <i onClick='window.history.back()'></i>
        <p>提现</p>
      </div>
    </div>
    <div class="cashBox">
      <div class="cashWhiteBox">
        <p class="imgBox"><img src="../../../static/images/icon_ragm@2x.png" alt=""></p>
        <p class="cashCur">USDT</p>
        <p class="cashName">Tether</p>
        <p class="balance">可提现</p>
        <p class="balanceNum">2521</p>
        <div class="numInputBox">
          <input type="number" placeholder="请输入提现数量">
          <p><span class="numCashCur">USDT</span><span class="allBtn">全部</span></p>
        </div>
        <input type="text" class="address" placeholder="输入或者粘贴地址">
        <p class="serverCharge"><span class="charge">手续费：2</span><span class="actual">实际到账：5</span></p>
        <button>提现</button>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "WithdRawal",
    data() {
      return {}
    }
  }
</script>

<style lang='less' scoped>
  .currencyTypes {
    background-color: #F5F5F5;
    height: 100vh;
    position: relative;
    .header {
      width: 3.75rem;
      background: #393A3E;
      height: 0.47rem;
      .title {
        overflow: hidden;
        position: relative;
        height: 0.47rem;
        p {
          font-weight: 500;
          color: rgba(255, 255, 255, 1);
          line-height: .47rem;
        }
        i {
          background: url("../../../static/btn_back_white@2x.png");
          background-size: cover;
          width: 0.16rem;
          height: 0.16rem;
          position: absolute;
          left: 0.2rem;
          top: 0.15rem;
        }
      }
    }
    .cashBox{
      padding: .15rem;
      .cashWhiteBox{
        background: #fff;
        border-radius: .03rem;
        padding: 0 .1rem .4rem;
        .imgBox{
          img{
            width: .35rem;
            height: .35rem;
            display: block;
            margin: auto;
            padding: .3rem 0 .09rem;
          }
        }
        .cashCur{
          color: #303030;
          font-size: .15rem;
          padding-bottom: .09rem;
        }
        .cashName{
          color: #84888C;
          font-size: .12rem;
          padding-bottom: .2rem;
        }
        .balance{
          color: #404040;
          font-size: .14rem;
        }
        .balanceNum{
          color: #000000;
          font-size: .3rem;
          font-weight: 800;
          font-family:PingFang SC;
        }
        .numInputBox{
          margin-top: .16rem;
          width: 100%;
          border: 1px solid #DDDDDD;
          border-radius: .03rem;
          overflow: hidden;
          height: .45rem;
          margin-bottom: .1rem;
          input{
            float: left;
            height: .44rem;
            padding-left: .1rem;
            width: 60%;
            color: #999999;
            font-size: .13rem;
            box-shadow: none;
          }
          p{
            float: right;
            line-height: .45rem;
            .numCashCur{
              color: #999;
              border-right: 2px solid #E33C3D;
              font-size: .13rem;
              padding-right: .12rem;
            }
            .allBtn{
              font-size: .14rem;
              color: #E33C3D;
              display: inline-block;
              height: .45rem;
              width: .5rem;
            }
          }
        }
        .address{
          display: block;
          box-shadow: none;
          height: .45rem;
          border: 1px solid #DDDDDD;
          border-radius: .03rem;
          padding-left: .1rem;
          color: #999999;
          font-size: .14rem;
          width: 3.15rem;
          margin-bottom: .08rem;
        }
        .serverCharge{
          overflow: hidden;
          color: #999999;
          font-size: .12rem;
          padding-bottom: .23rem;
          .charge{
            float: left;
            margin-left: .16rem;
          }
          .actual{
            float: right;
            margin-right: .16rem;
          }
        }
        button{
          width: 3.04rem;
          display: inline-block;
          background: #E33C3D;
          height: .45rem;
          border-radius: .03rem;
          color: #fff;
          font-family:PingFang SC;
          font-size: .18rem;
        }
      }
    }
  }
</style>



